                class="lists-name"
                :key="item.id"
                @click="handleProName(item.projectId, item.name)"
              >
                <a-tooltip :title="item.name">
                  <p class="project-name">{{ item.name }}</p>
                </a-tooltip>
              </div>
            </template>

            <!-- <a-drawer
              title="Select template"
              :closable="false"
              :visible="childrenDrawer"
              @close="onChildrenDrawerClose"
            >
              <p>功能升级中</p>
              <div :style="{
                  position: 'absolute',
