                  borderTop: '1px solid #e9e9e9',
                  padding: '10px 16px',
                  background: '#fff',
                  textAlign: 'right',
                  zIndex: 1,
                }">
                <a-button
                  :style="{ marginRight: '8px' }"
                  @click="onChildrenDrawerClose"
                >
                  Cancel
                </a-button>
                <a-button
                  type="primary"
                  @click="createWorkflow"
                >
                  Create directly
                </a-button>
              </div>
            </a-drawer> -->
          </a-drawer>
        </div>
        <div class="search">
          <input
            v-clickoutside="isResearch"
            type="text"
            placeholder="Search analysis workflow(Case sensitive)"
            v-model="value"
            ref="value"
            @focus="searchFocus"
          />
          <button v-if="isResearch" type="submit" @click="searchCancel">
            CANCEL
          </button>
          <button v-else type="submit">SEARCH</button>
        </div>
        <div class="search-result">
          <analysis-list
