<script>
import { mapState } from "vuex";
// 组件
import analyNav from "../../../PublicComponment/analyNav";
import analyFooter from "../../../PublicComponment/analyFooter";
import resultPage from "./components/resultsPage";
import workflowPage from "./components/workflowPage";
import projectList from "./components/indexComponents/projectList";
import analysisList from "./components/indexComponents/analysisList";
// 指令
import clickoutside from "../../../plugins/directivesSet/clickoutside";
// 导入外部js
import imgSuccess from "../../../static/png/success.png";
import imgRunning from "../../../static/png/running.png";
import imgFailed from "../../../static/png/failed.png";

export default {
  name: "analysisPage",

  components: {
    analyNav,
    analyFooter,
    projectList,
    resultPage,
    workflowPage,
    analysisList
  },

  data() {
    return {
      showID: 0, //限定Creating new project只在Analysis中出现
      currentTab: "Analysis",
      mainId: 0,
      // 创建新分析状态
      visible: false,
      childrenDrawer: false,
      // 存储项目信息
      projectData: [],
      // 占位框的loading
      listLoading: true,
      // 搜索数据暂存
      isResearch: false,
      value: "",
      isNoData: true,
      searchData: [],
      data: [],
      //
      isLeave: true,
      workflowName: "",

      imgSuccess: imgSuccess, // 状态图片
      imgRunning: imgRunning,
      imgFailed: imgFailed,

      // 是否开启下拉analysis history菜单
      isAnaHis: null,

      // result 节点信息
      nodesData: {},
      ProjectId: "" //获取click的projectId
    };
  },

  computed: {
    ...mapState({
      city: state => state.city,
      projectId: state => state.projectId.proInfo.projectId,
      projectName: state => state.projectId.proInfo.projectName,
      navWorkflow: state => state.titleWorkflow.navWorkflow
    })
  },

  // 自定义指令
  directives: {
    clickoutside
  },

  created() {
    this.requestProjectData();
    this.requestAnalysesData();
  },

  methods: {
    toWorkflow(e) {
      this.visible = false;
      if (e == 2) {
        let arr = [];
        arr.push(this.navWorkflow[0]);
        let obj = {
          title: "Analysis",
          sign: 2
        };
        arr.push(obj);
        this.$store.commit("postNavWorkflowData", arr);
        this.mainId = 0;
        this.requestProjectData();
        this.requestAnalysesData();
        this.listLoading = true;
        // 存储项目信息
        this.projectData = [];
        this.requestProjectData();
        this.requestAnalysesData();
      }
    },

    // 创建新workflow的方法
    showDrawer() {
      this.visible = true;
    },
    onClose() {
      this.visible = false;
    },
    handleProName(id, name) {
      let obj = {
        id: id,
        name: name
      };
      this.$store.commit("getProjectId", obj);
      sessionStorage.setItem("getProjectId", JSON.stringify(obj));
      this.$store.commit("getWorkflowInfo", null);
      // this.childrenDrawer = true;
      this.createWorkflow();
    },
    onChildrenDrawerClose() {
      this.childrenDrawer = false;
    },
    createWorkflow() {
      sessionStorage.removeItem("workflowState");
      this.childrenDrawer = false;
      setTimeout(() => {
        this.visible = false;
        this.mainId = 1;
        // 网页点击追踪
        let arr = [];
        arr.push(this.navWorkflow[0]);
        let obj_0 = {
          title: "Analysis",
          sign: 2
        };
        arr.push(obj_0);
        let obj_1 = {
          title: this.projectName,
          sign: 3
        };
        arr.push(obj_1);
        let obj_2 = {
          title: "Creating new workflow",
          sign: 4
        };
        arr.push(obj_2);
        this.$store.commit("postNavWorkflowData", arr);
      }, 0);
    },

    // 搜索的方法
    searchCancel() {
      this.value = "";
      this.isResearch = false;
    },
    searchFocus() {
      this.isResearch = true;
    },
    searchClick(e, f) {
      let obj = {
        id: e,
        name: f
      };
      this.$store.commit("getProjectId", obj);
      sessionStorage.setItem("getProjectId", JSON.stringify(obj));
    },

    // list点击 result
    handleToResult(val) {
      this.$progress.start();
      this.requestWorkflowData(val, 2);
    },

    // list点击 workflow
    handleToWorkflow(val) {
      this.$progress.start();
      this.requestWorkflowData(val, 1);
    },

    // 数据请求，以及数据恢复
    requestWorkflowData(val, mainId) {
      this.$ProjectModels
        .getWorkflowInfoModels(val.id, val.project.id)
        .then(val => {
          // 获取任务具体状态
          this.nodesData = val.data;
          if (val.data.nodes.length > 0) {
            let j = val.data.nodes[0].height;
            for (let i = 0; i < val.data.nodes.length; i++) {
              if (j < val.data.nodes[i].height) {
                j = val.data.nodes[i].height;
              }
            }
            this.nodesData.maxHeight = j;
          }
          let obj = {
            workflowId: val.id,
            nodesData: val.data
          };
          sessionStorage.setItem("workflowState", JSON.stringify(obj));
          if (val.data.length == 0) {
            this.$message.warning("No state");
          }
          this.$progress.finish();
          // 将服务器端的workflow重新渲染
          this.$store.commit("getWorkflowInfo", {
            nodes: [...val.data.nodes],
            paths: [...val.data.edges],
            workflowName: val.data.name,
            workflowId: val.id
          });

          // 将填写的工具信息重新收集
          let arr_nodes = [];
          val.data.nodes.forEach(ele => {
            let obj_nodes = {
              name: ele.name,
              id: ele.id,
              allName: ele.name,
              params: ele.params
            };
            arr_nodes.push(obj_nodes);
          });
          this.$store.commit("postworkflowNodeData", arr_nodes);
          this.mainId = mainId;
        });
    },

    //数据请求方法
    requestProjectData() {
      this.$progress.start(); // 进度条加载
      this.$ProjectModels.getProjectsModels().then(res => {
        var data = res.data.projects;
        data.forEach(ele => {
          this.projectData.push(ele);
        });
        this.listLoading = false;
        let dataProject = JSON.stringify(this.projectData);
        sessionStorage.setItem("dataProject", dataProject);
        this.$progress.finish(); // 进度条完成
      });
    },
    // 数据请求方法
    requestAnalysesData() {
      if (this.searchData.length == 0) {
        this.$ProjectModels.getAnalysesModels().then(res => {
          this.searchData = res.data;
          this.data = res.data;
        });
      } else {
        this.searchDatas();
      }
    },
    toAnalysis(e) {
      this.mainId = e;
    },
    dataFormUp(e) {
      this.mainId = e;
    },
    searchDatas(e) {
      this.searchData = e;
    },
    projectList(e) {
      this.searchData = e;
    }
  },
  watch: {
    value() {
      //search控制Analysis里的filter
      if (this.value == "") {
        this.searchData = this.data;
        return;
      }
      let list = []; //// 创建临时数组,承接数据
      for (let i in this.data) {
        if (
          this.data[i].name.toLowerCase().indexOf(this.value.toLowerCase()) >= 0
        ) {
          list.push(this.data[i]); // 查找每一项中是否存在输入值,如存在则push.
        }
      }
      if (list.length == 0) {
        this.searchData = [{ name: "No data" }];
        // this.isResearch = false;
      } else {
        this.searchData = list;
        this.isResearch = true;
      }
    }
  }
};
</script>

