<template>
  <div class="Heatmap">
    <div class="title">Heatmap</div>
       
    <a-divider style="margin:0.625rem 0;" />
    <change-name :nodeId="nodeId"></change-name>
       
    <a-divider dashed style="margin:0.625rem 0;" />
    <div class="title-second">Data transformation:</div>
    <div class="content-data">
      <a-radio-group @change="onChange" v-model="value">
        <a-radio :value="1">log</a-radio>
        <a-radio :value="2">zscore</a-radio>
      </a-radio-group>
    </div>

  </div>
</template>

<script>
import { mapState } from 'vuex';
import changeName from "./components/changeName";

export default {
  name: "Heatmap",

  props:{
    nodeId:{
      type:Number
    }
  },

  data(){
    return{
      value:1,
      params:{
      },
    }
  },

  computed: {
    ...mapState({
      workflowNodeInfo: state => state.workflowNode.workflowNodeInfo // 暂存每个已有的节点信息
    })
  },

  components:{
    changeName
  },

  mounted(){
    this.recoverData(this.nodeId);
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    // 重新记录当前单选框记录
    if (this.value === 1) {
      this.params.quantification = 'log'
    } else {
      this.params.quantification = 'zscore'
    }
    this.params.quantification = this.quantification;
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach(element => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit('postworkflowNodeData', arr);
  },


  methods:{
    onChange(e){
      if(e.target.value == 1){
        this.params.quantification = 'log'
      }else{
        this.params.quantification = 'zscore'
      }
    },

     // 复用函数，复原数据
    recoverData(a){
      this.workflowNodeInfo.forEach(element => {
        if (element.id == a && Object.keys(element.params).length !== 0) {
          if (element.params.quantification == "log") {
            this.value = 1;
          } else {
            this.value = 2;
            this.quantification = "zscore"
          }
        }
      });
    }
  }
};
</script>

