<template>
  <div class="GSEA">
    <div class="title">GSEA</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin: 0.625rem 0" />
    <div class="filter">
      <div class="title-second">library:</div>
      <span>Choose to get the library</span>
      <a-select style="width: 100%" v-model="params.library">
        <a-select-option v-for="i in options" :key="i" :value="i.value">{{
          i.value
        }}</a-select-option>
      </a-select>
      <div class="title-second">species:</div>
      <a-radio-group
        v-model="params.species"
        :options="species"
        @change="onChange"
      />
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";
const options = [
  {
    value: "GO",
  },
  {
    value: "KEGG",
  },
  {
    value: "REACTOME",
  },
  {
    value: "HALLMARKS",
  },
];
const species = [
  {
    label: "Human",
    value: "Human",
  },
  {
    label: "Mouse",
    value: "Mouse",
  },
];
export default {
  name: "GSEA",
  props: {
    nodeId: {
      type: Number,
    },
  },
  data() {
    return {
      options,
      species,
      params: {
        data: {},
        library: "",
        species: "",
      },
    };
  },
  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    // this.params.MAFNumber = this.messageTags;
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach((element) => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },
  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },
  components: {
    changeName,
  },
  mounted: {
    onChange(e) {
      this.params.species = e.target.value;
    },
  },
};
</script>

<style lang="scss" scoped>
.GSEA {
  padding: 0rem 1.25rem 1.875rem 1.25rem;
  .title {
    font-weight: 600;
  }
  .filter {
    .title-second {
      font-size: 0.9rem;
      font-weight: 800;
    }
  }
}
</style>