      let nodeId = this.resultNodeId;
      let name = this.stateName;
      this.getPlotFile(workflowId, id, nodeId, name);
    },

    // 复用函数 （表格文件）
    getTableFile(workflowId, id, nodeId, name) {
      let isFile = true;
      let fileName = "result";
      this.$progress.start();
      this.$ProjectModels
        .getTableResultModelsT(workflowId, id, nodeId, name, isFile, fileName)
        .then((ele) => {
          if (!ele) {
            return;
          }
          // window.console.log(eve.data.type.slice(12))
          var filename = this.stateName; // 创建文件的file name
          let url = window.URL.createObjectURL(ele.data); // 将对象转为一个url
          let link = document.createElement("a"); //创建一个a标签
          link.style.display = "none"; // 清除样式
          link.href = url; // 将
          link.setAttribute(
            "download",
            filename + "." + ele.data.type.slice(12)
          );
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
          this.$progress.finish();
        })
        .catch((error) => {
          this.$message.error(
            "Failed to update the data. Please try again or check the network." +
            error,
            5
          );
        });
    },

