    <div id="barPlot" class="plot"></div>
    <div class="slider">
      <div class="download">
        <div class="file-name" v-for="item in fileNames" :key="item.index">
          <div class="btn">
            <div class="btn-download" @click="download(item)">
              <a-button ghost icon="download" type="primary">
                download
              </a-button>
            </div>
            <div class="btn-preview" @click="preview(item)">
              <a-icon type="eye" />
            </div>
          </div>
          <div class="text-item">
            {{ item }}
          </div>
        </div>
      </div>

      <div class="sub-project">
        <div class="label-button">
          <h1>generate new quantification matrix</h1>
          <div class="line" style="width: 88%"></div>
          <div class="cover-content">
            <!-- <div class="cover-label">
              <template v-for="(item, index) in labels">
                <a-tooltip
                  v-if="item.name.length > 20"
