            使用新的筛选后的数据添加新的任务
            <button class="run-task" @click.once="submitData">running</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
// 颜色数组
import * as echarts from "echarts";
import colors from "../../../../../utils/colors";
import state from "../../../../../store/state";
export default {
  name: "barPlot",
  components: {},
  data() {
    return {
      // yanse
      colors: [],
      // 绘图数据
      plotData: {},
      barPlotData: {
        xAxis: {
          type: "category",
          axisLabel: {
            interval: 0,
            rotate: 40,
          },
          data: [],
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            type: "bar",
            data: [],
            itemStyle: {
              color: "#1F77B4",
            },
          },
        ],
      },
      fileNames: [],
      // 表格展示文件
      fileName: "result",
      // 是否显示添加label输入框
      labels: [],
    };
  },
  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      workflowId: (state) => state.workflowId.workflowId,
      resultNodeId: (state) => state.workflowId.resultNodeId,
      stateName: (state) => state.stateName.stateName,
    }),
  },
  watch: {
    resultNodeId() {
      let id = this.projectId;
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      this.getPlotData(workflowId, id, nodeId, name);
      if (this.stateName != "FAGL") {
        this.getFileData();
      }
      this.labels = [];
    },
    workflowId() {
      let id = this.projectId;
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      this.getPlotData(workflowId, id, nodeId, name);
    },
  },
  methods: {
    drawImage() {
      let canvas = document.getElementById("barPlot");
      // // 初始化开始
      // canvas.setAttribute('width', this.$refs.consize.offsetWidth);
      // canvas.setAttribute('height', this.$refs.consize.offsetHeight);
      // 绘图函数

      var myChart = echarts.init(canvas);
      myChart.setOption(this.barPlotData);
    },
    getPlotData(workflowId, id, nodeId, name) {
      let isFile = false; // 请求图表的坐标数据，所以直接设置为false
      this.$progress.start();
      this.$ProjectModels
        .getPlotResultModelsF(workflowId, id, nodeId, name, isFile)
        .then((val) => {
          console.log(val);
          this.barPlotData.xAxis.data = val.data.bars;
          this.barPlotData.series[0].data = val.data.height;
          this.$store.commit("getStateName", name); // 储存点击的node name；
          this.$store.commit("pushCollects", this.plotData); //储存数据在vuex
          this.workflowName = val.data.name;
          if (val.data.length == 0) {
            this.$message.warning("No data");
          }
          this.$progress.finish();
        })
        .then((res) => {
          this.drawImage();
        });
    },
    download(fileName) {
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      let id = this.projectId;
      let isFile = true;
      this.getTableFile(workflowId, id, nodeId, name, isFile, fileName);
    },
    preview(fileName) {
      this.fileName = fileName;
      this.getData();
    },
    getFileData() {
      let workflowId = this.workflowId;
      let nodeId = sessionStorage.getItem("getResultNodeId");
      let name = this.stateName;
      let id = this.projectId;
      this.$progress.start();
      this.$ProjectModels
        .getTaskNodeModels(workflowId, id, nodeId, name)
        .then((inx) => {
          this.fileNames = inx.data;
          if (inx.data.length == 0) {
            this.$message.warning("No task");
          }
          this.$progress.finish();
        });
    },

    // download表格文件
    getTableFile(workflowId, id, nodeId, name, isFile, fileName) {
      this.$progress.start();
      this.$ProjectModels
        .getTableResultModelsT(workflowId, id, nodeId, name, isFile, fileName)
        .then((ele) => {
          if (!ele) {
            return;
          }
          let url = window.URL.createObjectURL(ele.data); // 将对象转为一个url
          let link = document.createElement("a"); //创建一个a标签
          link.style.display = "none"; // 清除样式
          link.href = url; // 将
          link.setAttribute("download", fileName);
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);

          this.$progress.finish();
        });
    },
  },
  created() {
    // 获取颜色
    this.colors = colors.colors;
    // 读取记录的ResultNodeId，以便渲染图
    this.$store.commit(
      "getResultNodeId",
      sessionStorage.getItem("getResultNodeId")
    );
  },

  mounted() {
    if (this.plotData.length == 0) {
      let id = this.projectId;
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      this.getPlotData(workflowId, id, nodeId, name);
    }
  },
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  activated() {},
};
</script>
