<template>
  <div
    class="ScatterPlot"
    ref="consize"
    v-resize="resize"
  >
    <div
      id="ScatterPlot"
      :style="{ height: screenHeight - 60 + 'px',width:'100%'}"
    ></div>

    <!-- <div
        id="ScatterPlot"
        :style="{ height:'90%'}"
      ></div> -->

  </div>
</template>

<script>
import { mapState } from "vuex";
import resize from "../../../../../../plugins/directivesSet/resize";

export default {
  name: "ScatterPlot",

  props: {
    plotData: {
      type: [Object, Array],
    },
    secondData: {
      type: [Object, Array],
    },
  },

  data() {
    return {
      screenHeight: 0,
      positionData: [],
      max_x: "",
      min_x: "",
      max_y: "",
      min_y: "",
      xlab: "",
      ylab: "",
      symbolSize: "",
      markArea: "",
      series: [],
      fileValue: [],
      symbolName: [],
    };
  },

  // 自定义指令
  directives: {
    resize,
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      workflowId: (state) => state.workflowId.workflowId,
      resultNodeId: (state) => state.workflowId.resultNodeId,
      stateName: (state) => state.stateName.stateName,
    }),
  },

  mounted() {
    this.screenHeight = this.$refs.consize.offsetHeight;
    this.drawImage();
  },
  methods: {
    drawImage() {
      let canvas = document.getElementById("ScatterPlot");
      // // 初始化开始
      // canvas.setAttribute('width', this.$refs.consize.offsetWidth);
      // canvas.setAttribute('height', this.$refs.consize.offsetHeight);
      // 绘图函数
      var myChart = this.$echarts.init(canvas);

      // 调用showLoading方法
      myChart.showLoading({
        text: "loading",
        color: "#c23531",
        textColor: "#000",
        maskColor: "rgba(255, 255, 255, 0.2)",
        zlevel: 0,
      });

      // setOption前隐藏loading事件
      myChart.hideLoading();
      myChart.setOption({
        tooltip: {
          padding: 10,
          backgroundColor: "#222e",
          borderColor: "#777",
          formatter: function (obj) {
            var value = obj.value;
            return `NAME: ${value[3]} ${'<br>'} ${value[0].toFixed(2)} ${'<br>'}${value[1].toFixed(2)}`
          },
        },
        legend: {
          top: 50,
          orient: "vertical",
          left: 'right',
          data: this.symbolName,
          textStyle: {
            fontSize: 18
          },

        },
        xAxis: {
          type: "value",
          name: `${this.plotData.ylab}(${this.plotData.explained_variance_PC2 * 100}%)`,
          nameGap: 16,
          nameTextStyle: {
            color: "#808080",
            fontSize: 13,
          },
          nameLocation: "start",
          min: this.plotData.minX,
          max: this.plotData.maxX,
          splitLine: {
            // show: false,
            lineStyle: {
              type: "dashed",
            },
          },
          axisLine: {
            lineStyle: {
              color: "#808080",
            },
          },
          scale: true,
        },
        yAxis: {
          min: this.plotData.minY,
          max: this.plotData.maxY,
          type: "value",
          name: `${this.plotData.xlab}(${this.plotData.explained_variance_PC1 * 100}%)`,
          nameLocation: "start",
          nameGap: 20,
          nameTextStyle: {
            color: "#808080",
            fontSize: 13,
          },
          axisLine: {
            lineStyle: {
              color: "#808080",
            },
          },
          splitLine: {
            // show: false,
            lineStyle: {
              type: "dashed",
            },
          },
          scale: true,
        },

        series: this.series,
      });

      myChart.resize();
    },

    // 根据指令集的变化记录当前元素的宽高
    resize() {
      // 父元素宽高变化时调用绘图函数
      this.screenHeight = this.$refs.consize.offsetHeight;
      this.drawImage();
    },
  },

  watch: {
    // 实时对获取到的数据进行修改，并触发重绘函数
    plotData() {
      this.colors = this.plotData.colors;

      this.series = [];
      Object.keys(this.plotData.data).forEach((element) => {
        this.positionData = this.plotData.data[element].reduce(function (
          coll,
          item
        ) {
          coll.push(item);
          return coll;
        },
          this.positionData);
        this.symbolName.push(element);
        let obj = {
          name: element,
          symbolSize: 6,
          data: this.plotData.data[element],
          type: "scatter",
          itemStyle: {
            color: this.colors[element],
          },
        };
        this.series.push(obj);
      });

      this.drawImage();
      this.max_x = this.plotData.maxX;
      this.min_x = this.plotData.minX;
      this.max_y = this.plotData.maxY;
      this.min_y = this.plotData.minY;
      this.xlab = this.plotData.xlab;
      this.ylab = this.plotData.ylab;

      let arr = {
        max_x: this.max_x,
        min_x: this.min_x,
        max_y: this.max_y,
        min_y: this.min_y,
        symbolSize: "NA",
        markArea: "NA",
        color: "red",
      };
      this.fileValue.push(arr);
    },
    secondData() {
      if (this.secondData.lenght == 0) {
        this.series = [];
        Object.keys(this.plotData.data).forEach((element) => {
          this.positionData = this.plotData.data[element].reduce(function (
            coll,
            item
          ) {
            coll.push(item);
            return coll;
          },
            this.positionData);
          this.symbolName.push(element);
          let obj = {
            name: element,
            symbolSize: 6,
            data: this.plotData.data[element],
            type: "scatter",
            itemStyle: {
              color: this.colors[element],
            },
          };
          this.series.push(obj);
        });
        this.drawImage();
      } else {
        this.series = [];
        Object.keys(this.plotData.data).forEach((element) => {
          this.positionData = this.plotData.data[element].reduce(function (
            coll,
            item
          ) {
            coll.push(item);
            return coll;
          },
            this.positionData);
          this.symbolName.push(element);
          let obj = {
            name: element,
            symbolSize: 6,
            data: this.plotData.data[element],
            type: "scatter",
            itemStyle: {
              color: this.colors[element],
            },
          };
          this.series.push(obj);
        });
        this.series.push(...this.secondData);
        this.drawImage();
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.ScatterPlot {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 1.875rem;
  background: #fff;
  box-shadow: .125rem .125rem 1.25rem rgba(25, 25, 25, .1);
  border-radius: 1.5625rem;
}
</style>