<template>
  <div class="changeName">
    <div class="title-second">Change node name:</div>
    <div class="change-name" v-clickoutside="inputNameShow">
      <div class="show-name" @click="showInputName">
        <div class="show-name-span" v-show="!inputNameShow" :title="nodeName">
          {{ nodeName }}
        </div>
        <div class="show-name-input">
          <input type="text" v-model="nodeName" ref="inputName" />
        </div>
      </div>
      <div @click="showInputName" class="show-input-btn">
        <a-icon type="edit" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
// 自定义指令
import clickoutside from "../../../../../../../plugins/directivesSet/clickoutside";

export default {
  name: "changeName",

  props: {
    nodeId: {
      type: Number,
    },
  },

  data() {
    return {
      // 自定义节点名称
      nodeName: "",
      // 是否开启修改
      inputNameShow: false,
    };
  },

  directives: {
    clickoutside,
  }, // 自定义指令

  computed: {
    ...mapState({
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },

  mounted() {
    // this.nodeName = this.workflowNodeInfo[this.nodeId].allName;
    // window.console.log(this.workflowNodeInfo)
  },

  methods: {
    // 开启workflow名称输入框
    showInputName() {
      this.inputNameShow = true;
      this.$refs.inputName.focus();
    },
  },

  watch: {
    nodeName() {
      let arr = Array.from(this.workflowNodeInfo);
      // 溯源
      arr.forEach((element) => {
        // 数据更新
        if (element.id == this.nodeId) {
          element.allName = this.nodeName;
        }
      });
      // 记录到vuex
      this.$store.commit("postworkflowNodeData", arr);
      // this.$store.commit('getWorkflowInfo');
    },
  },
};
</script>

<style lang="scss" scoped>
.changeName {
  .title-second {
    font-weight: 550;
    margin: 0.9375rem 0 0.9375rem 0;
  }
  .change-name {
    display: flex;
    justify-content: space-between;
    padding: 0rem 1.25rem 0rem 1.25rem;
    height: 2rem;
    line-height: 2rem;
    border-radius: 1.5625rem;
    background: #f1f1f1;
    .show-name {
      width: 90%;
      cursor: pointer;
      position: relative;
      .show-name-span {
        width: 100%;
        background: #f1f1f1;
        overflow: hidden; //超出的文本隐藏
        text-overflow: ellipsis; //溢出用省略号显示
        white-space: nowrap; //溢出不换行
        position: absolute;
        z-index: 999;
      }
      .show-name-input {
        width: 100%;
        input {
          background: #f1f1f1;
          width: 100%;
          border: none;
          outline: none;
        }
      }
    }

    .show-input-btn {
      width: 10%;
      font-weight: 600;
      text-align: center;
      cursor: pointer;
    }
  }
}
</style>