<template>
  <div class="SVM">
    <div class="title">Support Vector Machine</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin: 0.625rem 0" />

    <div class="title-second">Feature Transform:</div>
    <div class="content-data">
      <a-select
        v-model="defaultValue_F"
        size="small"
        style="width: 100%"
        @change="handleChange_F"
      >
        <a-select-option value="decisionTree"> decisionTree </a-select-option>
      </a-select>
    </div>

    <a-divider dashed style="margin: 0.625rem 0" />

    <div class="title-second">Test Size:</div>
    <div class="content-data">
      <div class="strict-peptide-number">
        <a-input placeholder="Test Size Number" v-model="testSizeNum" />
      </div>
    </div>

    <a-divider dashed style="margin: 0.625rem 0" />

    <div class="title-second">Model Validation:</div>
    <div class="content-data">
      <a-select
        v-model="defaultValue_M"
        size="small"
        style="width: 100%"
        @change="handleChange_M"
      >
        <a-select-option value="ROC"> ROC </a-select-option>
      </a-select>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";

export default {
  name: "SVM",

  props: {
    nodeId: {
      type: Number
    }
  },

  data() {
    return {
      params: {},
      testSizeNum: "",
      defaultValue_F: "",
      defaultValue_M: ""
    };
  },

  computed: {
    ...mapState({
      projectId: state => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: state => state.workflowNode.workflowNodeInfo // 暂存每个已有的节点信息
    })
  },

  components: {
    changeName
  },

  mounted() {
    this.recoverData(this.nodeId);
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    this.params.featureSelect = this.defaultValue_F;
    this.params.testSize = this.testSizeNum;
    this.params.validation = this.defaultValue_M;

    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach(element => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },

  methods: {
    handleChange_F(e) {
      this.defaultValue_F = e;
    },

    handleChange_M(e) {
      this.defaultValue_M = e;
    },

    // 复用函数，复原数据
    recoverData(a) {
      var that = this;
      that.workflowNodeInfo.forEach(element => {
        if (element.id == a && JSON.stringify(element.params) !== "{}") {
          this.defaultValue_F = element.params.featureSelect;
          this.defaultValue_M = element.params.validation;
          this.testSizeNum = element.params.testSize;
        }
        if (element.id == a && JSON.stringify(element.params) == "{}") {
          this.defaultValue_F = "";
          this.defaultValue_M = "";
          this.testSizeNum = "";
        }
      });
    }
  }
};
</script>

