<template>
  <div class="VolcanoPlot">
    <div class="title">PCA</div>
       
    <a-divider style="margin:0.625rem 0;" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin:0.625rem 0;" />
    <div class="title-second">instance:</div>
    <div class="content-data">
      <a-radio-group @change="onChange_0" v-model="value">
        <a-radio :value="1">row</a-radio>
        <a-radio :value="2">column</a-radio>
      </a-radio-group>
    </div>


    <a-divider dashed style="margin:0.625rem 0;" />
    <div class="title-second">Number of Components:</div>
    <div class="content-data">
      <a-input placeholder="Number of Components" v-model="numberOfComponents" ref="numberOfComponents" size="small">
        <a-icon v-if="numberOfComponents" slot="suffix" type="close-circle" @click="emitEmpty"/>
      </a-input>
    </div>

  </div>
</template>

<script>
import { mapState } from 'vuex';
import changeName from "./components/changeName";

export default {
  name: "VolcanoPlot",

  props:{
    nodeId:{
      type:Number
    }
  },

  data() {
    return {
      // 单选框数据
      value: 1,
      instance: 'row',
      // 数字输入框
      numberOfComponents: 2,
      params: {},
    };
  },

  computed: {
    ...mapState({
      projectId: state => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: state => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    })
  },
    
  components:{
    changeName
  },

  mounted() {
    this.recoverData(this.nodeId);
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex;
    this.params = {
      instance: this.instance,
      numberOfComponents:  Number(this.numberOfComponents) 
    };
    let obj = Object.assign({}, this.params);
    let arr = Array.from(this.workflowNodeInfo);
    arr.forEach(element => {
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit('postworkflowNodeData', arr);
  },

  methods: {
    onChange_0(e) {
      if (e.target.value == 1) {
        this.instance = 'row';
      } else {
        this.instance = 'column';
      }
    },

     //输入框清空
    emitEmpty(){
      this.$refs.numberOfComponents.focus();
      this.numberOfComponents = "";
    },

    // 复用函数，复原数据
    recoverData(a) {
      this.workflowNodeInfo.forEach(element => {
        if (element.id == a) {
          if(Object.keys(element.params).length !== 0){
            if (element.params.instance == 'row') {
              this.value = 1;
            } else {
              this.value = 2;
              this.instance = "column"
            }
            this.numberOfComponents = element.params.numberOfComponents
          }
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.VolcanoPlot {
  padding: 0rem 1.25rem 1.875rem 1.25rem;
  .title {
    font-weight: 600;
    font-style: italic;
  }
  .title-second {
    font-weight: 550;
    margin: 0.9375rem 0 0.9375rem 0;
  }
  .title-third {
    font-size: 0.75rem;
    margin: 0.9375rem 0 0.3125rem 0;
  }
  .content-data {
    padding: 0rem 0rem 0.625rem 0.9375rem;
  }
}
</style>