<template>
  <div class="modifyScatter">
    <p
      class="download-dre"
      style="margin-left: 1.875rem"
    >
      如果你想下载原图，请点击 <a @click="downloadOriginal">这里</a>
    </p>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />

    <div class="condition">
      <div class="title">Custom chart style modification</div>
      <div class="max-and-min content">
        <div class="box-title">
          <p>{{ xlab }} &nbsp; &lt;=</p>
          <input
            class="max-x"
            type="text"
            v-model="max_x"
          />
        </div>
        <div class="box-title">
          <p>{{ xlab }} &nbsp; &gt;=</p>
          <input
            class="min-x"
            type="text"
            v-model="min_x"
          />
        </div>
        <div class="box-title">
          <p>{{ ylab }} &nbsp; &lt;=</p>
          <input
            class="max-y"
            type="text"
            v-model="max_y"
          />
        </div>
        <div class="box-title">
          <p>{{ ylab }} &nbsp; &gt;=</p>
          <input
            class="min-y"
            type="text"
            v-model="min_y"
          />
        </div>
      </div>
      <div class="other-condition">
        <div>
          <p class="box2-title">Symbol size</p>
          <input
            class="symbol-size"
            type="text"
            v-model="symbolSize"
          />
        </div>
        <div>
          <p class="box2-title">Area name</p>
          <input
            class="mark"
            type="text"
            v-model="markArea"
          />
        </div>

        <div class="form__input">
          <p
            class="box2-title"
            style="margin-bottom: 1.125rem"
          >color</p>
          <swatches
            v-model="color"
            colors="text-advanced"
            popover-to="left"
            swatch-Size="30"
            shapes="circles"
          ></swatches>
        </div>
      </div>
    </div>

    <button
      class="draw-condition"
      @click="secondDraw"
    >Draw</button>

    <div
      class="handle-condition"
      v-for="(itme, index) in fileValue"
      :key="index"
    >
      <div style="margin-bottom: 0.375rem">
        &nbsp; &nbsp; &nbsp;Value range is
        <span>{{ itme.min_x }}</span>
        &lt; {{ xlab }} &lt;
        <span>{{ itme.max_x }}</span>
        and
        <span>{{ itme.min_y }}</span>
        &lt; {{ ylab }} &lt; <span>{{ itme.max_y }}</span>, symbol size is <span>{{ itme.symbolSize }}</span>, area name is <span>{{ itme.markArea }}</span>, color is <span>{{ itme.color }}</span>.
        <div
          style="
            color: #1ca085;
            display: inline-block;
            cursor: pointer;
            font-weight: 600;
          "
          @click="deleteCondition(index)"
        >
          delete
        </div>
      </div>
    </div>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />

    <div>
      <div class="title">Data transfer</div>
      <div class="plot-draw-data">
        <div class="box3-title">
          <p>Abscissa</p>
          <input
            type="text"
            v-model="xlab_"
          />
        </div>
        <br />
        <div class="box3-title">
          <p>Ordinate</p>
          <input
            type="text"
            v-model="ylab_"
          />
        </div>
      </div>
      <p
        class="download-dre"
        style="margin-left: 1.875rem"
      >
        提交新修改绘制图表样式，请点击 <a @click="returnData">这里</a>
      </p>
      <p
        class="download-dre"
        style="margin-left: 1.875rem"
      >
        更新修改任务列表，请点击 <a @click="checkState">这里</a>
      </p>
      <a-button
        class="sendResult"
        type="primary"
        @click="sendResult"
      >
        send Result
      </a-button>
      <div
        class="demo-infinite-container"
        v-infinite-scroll="handleInfiniteOnLoad"
        :infinite-scroll-distance="10"
        style="margin-left: 1.875rem"
      >
        <a-list :dataSource="stateData">
          <a-list-item
            slot="renderItem"
            slot-scope="item"
          >
            <a-list-item-meta :description="item.submmit_time">
              <a
                slot="title"
                class="state-button"
                @click="getPlotData(item.taskId)"
              >download</a>
            </a-list-item-meta>
            <!-- <div>{{ item.state }}</div> -->
          </a-list-item>
        </a-list>
      </div>
      <div
        v-if="loading_1"
        class="demo-loading-container"
        style="margin-left: 1.875rem"
      >
        <a-spin />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import infiniteScroll from "vue-infinite-scroll";
import Swatches from "vue-swatches";
import "vue-swatches/dist/vue-swatches.min.css";

export default {
  name: "modifyScatter",

  props: {
    plotData: {
      type: [Object, Array],
    },
  },

  data() {
    return {
      // 横坐标最大值
      max_x: "",
      // 横坐标最小值
      min_x: "",
      // 纵坐标最大值
      max_y: "",
      // 纵坐标最大值
      min_y: "",
      // 横坐标 name
      xlab: "",
      // 纵坐标 name
      ylab: "",
      // 图的点大小
      symbolSize: "",
      // 范围名称
      markArea: "",
      // 取色器初始颜色
      color: "#1CA085",
      // 总数据量
      positionData: [],
      fileValue: [],
      series: [],
      // 数据修改(横坐标)
      xlab_: "",
      // 数据修改(纵坐标)
      ylab_: "",
      // 图返回数据
      loading: false,
      loading_1: false,
      scroll: {},
      stateData: [],
    };
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      workflowId: (state) => state.workflowId.workflowId,
      resultNodeId: (state) => state.workflowId.resultNodeId,
      stateName: (state) => state.stateName.stateName,
    }),
  },

  directives: {
    infiniteScroll,
  },

  components: {
    Swatches,
  },

  mounted() { },

  methods: {
    // 数据整理函数
    addCondition(testX, _testX, testY, _testY, symbolSize, name) {
      Object.keys(this.plotData.data).forEach((element) => {
        this.positionData = this.plotData.data[element].reduce(function (
          coll,
          item
        ) {
          coll.push(item);
          return coll;
        },
          this.positionData);
      });
      // 添加修改
      let aim = {
        symbolSize: symbolSize,
        data: [],
        type: "scatter",
        color: this.color,
        markArea: {
          silent: true,
          itemStyle: {
            normal: {
              color: "transparent",
              borderWidth: 1,
              borderType: "dashed",
            },
          },
          data: [
            [
              {
                name: name,
                xAxis: "min",
                yAxis: "min",
              },
              {
                xAxis: "max",
                yAxis: "max",
              },
            ],
          ],
        },
      };
      for (let i = 0; i < this.positionData.length; i++) {
        if (
          this.positionData[i][0] <= testX &&
          this.positionData[i][0] >= _testX &&
          this.positionData[i][1] <= testY &&
          this.positionData[i][1] >= _testY
        ) {
          aim.data.push(this.positionData[i]);
        }
      }
      return aim;
    },

    // 绘图信息枚举
    fileCondition() {
      let arr = {
        max_x: this.max_x,
        min_x: this.min_x,
        max_y: this.max_y,
        min_y: this.min_y,
        symbolSize: this.symbolSize,
        markArea: this.markArea,
        color: this.color,
      };
      if (this.symbolSize == "" || this.markArea == "") {
        arr.symbolSize = 5;
        arr.markArea = "Unnamed";
      }
      this.fileValue.push(arr);
    },

    deleteCondition(e) {
      this.series.splice(e, 1);
      this.fileValue.splice(e, 1);
      this.$emit("secondDraw", this.series);
    },

    // 提交二次绘制任务
    secondDraw() {
      let input = [
        this.max_x,
        this.min_x,
        this.max_y,
        this.min_y,
        this.symbolSize,
        this.markArea,
      ];
      this.series.push(this.addCondition(...input));
      this.$emit("secondDraw", this.series);
      this.fileCondition();
    },

    // 下载原图
    downloadOriginal() {
      let id = this.projectId;
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      // 所有节点的原图名称都为'plot.pdf'
      let fileName = "plot.pdf";
      // 引用下载pdf函数
      this.getPlotFile(workflowId, id, nodeId, name, fileName);
    },

    // 重新提交修改后的数据
    returnData() {
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let id = this.projectId;
      // 暂时写死
      let plotType = "scatterPlot";
      let arr = [];
      this.fileValue.forEach((ele) => {
        let obj = {
          xaxis: [ele.min_x, ele.max_x],
          yaxis: [ele.min_y, ele.max_y],
          color: ele.color,
          size: ele.symbolSize,
        };
        arr.push(obj);
      });

      let data = {
        xaxis: {
          data: this.xlab_,
          from: "result",
          transform: [],
        },
        yaxis: {
          data: this.ylab_,
          from: "result",
          transform: [],
        },
        blocks: [...arr],
      };
      this.$progress.start();
      this.$ProjectModels
        .postPlotTaskModels(workflowId, id, nodeId, plotType, data)
        .then((res) => {
          // this.weizhi = res.data;
          window.console.log(res);
          this.$progress.finish();
        });
    },

    checkState() {
      // 更新任务列表
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      this.$progress.start();
      this.$ProjectModels
        .getTaskNodeStateModels(workflowId, nodeId)
        .then((res) => {
          window.console.log(res.data.states);
          this.stateData = res.data.states;
          this.$progress.finish();
        });
    },

    handleInfiniteOnLoad() {
      this.loading_1 = false; // 任务列表的加载动画
    },

    getPlotData(e) {
      let that = this;
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let id = this.projectId;
      let taskId = e;
      this.$progress.start();
      this.$ProjectModels
        .getPlotResultFileModels(id, workflowId, taskId, nodeId)
        .then((eve) => {
          if (!eve) {
            return;
          }
          var filename = this.stateName;
          let url = window.URL.createObjectURL(eve.data);
          let link = document.createElement("a");
          link.style.display = "none";
          link.href = url;
          link.setAttribute(
            "download",
            filename + "." + eve.data.type.slice(12)
          );
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
          if (eve.data.length == 0) {
            that.$message.warning("No task");
          }
          this.$progress.finish();
        });
    }, // download文件

    // 复用函数 （下载图文件）
    getPlotFile(workflowId, id, nodeId, name, fileName) {
      let isFile = true;
      this.$ProjectModels
        .getPlotResultModelsT(workflowId, id, nodeId, name, isFile, fileName)
        .then((eve) => {
          if (!eve) {
            return;
          }
          var filename = this.stateName;
          let url = window.URL.createObjectURL(eve.data);
          let link = document.createElement("a");
          link.style.display = "none";
          link.href = url;
          link.setAttribute(
            "download",
            filename + "." + eve.data.type.slice(12)
          );
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
          if (eve.data.length == 0) {
            this.$message.warning("No task");
          } else {
            this.$message.success("Get chart data successfully", 3);
          }
        });
    },
    sendResult() {
      let name = "*****";
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let nodeName = this.stateName;
      let categoryprojectId = "";
      let projectId = this.projectId;
      var data = {
        name,
        workflowId,
        nodeId,
        nodeName,
        categoryprojectId,
      };
      this.$ProjectModels
        .saveResultModels(projectId, data, workflowId)
        .then((res) => {
          console.log(res);
        });
    },
  },

  watch: {
    plotData() {
      this.max_x = this.plotData.maxX;
      this.min_x = this.plotData.minX;
      this.max_y = this.plotData.maxY;
      this.min_y = this.plotData.minY;
      this.xlab = this.plotData.xlab;
      this.xlab_ = this.plotData.xlab;
      this.ylab = this.plotData.ylab;
      this.ylab_ = this.plotData.ylab;
    },
  },
};
</script>

