<template>
  <div class="analysis-list">
    <ul class="data-table">
      <!-- 占位 -->
      <div v-if="loading" style="padding: 0 3.125rem 0 3.125rem">
        <a-skeleton avatar :paragraph="{ rows: 2 }" :loading="loading" active />
        <div style="margin-top: 2.5rem"></div>
        <a-skeleton avatar :paragraph="{ rows: 2 }" :loading="loading" active />
        <div style="margin-top: 2.5rem"></div>
        <a-skeleton avatar :paragraph="{ rows: 2 }" :loading="loading" active />
      </div>
      <li
        v-for="items in searchData"
        :key="items.id"
        class="time"
        v-else-if="isNoData"
      >
        <div class="data-show">
          <div class="DOI-link">
            <a>{{ items.idx }} </a>
          </div>
          <div class="data-name">
            <h2>
              {{ items.name }}
              <span style="font-size: 1rem; font-weight: 400"
                >in {{ items.project.name }}</span
              >
            </h2>
            <p>
              Created at
              <span>{{ items.createDate }}</span>
            </p>
          </div>
          <div class="data-date">
            <p>
              Start Date: <span>{{ items.startDate }}</span>
            </p>
            <p>
              End Date: <span>{{ items.endDate }}</span>
            </p>
          </div>
          <div class="data-status">
            <div v-if="items.status === 'success'">
              <img
                style="height: 2rem; width: 2rem; cursor: pointer"
                :src="imgSuccess"
              />
            </div>
            <div v-else-if="items.status === 'running'">
              <img
                style="height: 2rem; width: 2rem; cursor: pointer"
                :src="imgRunning"
              />
            </div>
            <div v-else-if="items.status === 'failed'">
              <img
                style="height: 2rem; width: 2rem; cursor: pointer"
                :src="imgFailed"
              />
            </div>
          </div>
        </div>
        <div class="arrow">
          <button
            class="arrow-btn"
            @click="openOptions(items.idx)"
            v-clickoutside="isOptions"
          >
            Options
            <template v-if="items.idx == isAnaHis">
              <icon-font type="icon-xiangshang" />
            </template>
            <template v-else>
              <icon-font type="icon-xiangxia" />
            </template>
          </button>

          <div class="option-list" v-if="items.idx == isAnaHis">
            <div class="lists-name">
              <div class="project-name" @click="toResult(items, 1)">
                Modify analysis workflow
              </div>
            </div>
            <div class="lists-name">
              <div class="project-name" @click="toResult(items, 2)">
                View workflow results
              </div>
            </div>
            <div class="lists-name">
              <div class="project-name" @click="downLoad(items)">
                Download Results
              </div>
            </div>
            <div class="lists-name">
              <div class="project-name" @click="del(items)">
                Delete workflow
              </div>
            </div>
          </div>
        </div>
      </li>
      <div v-else class="no-data">NO DATA</div>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";
// 导入iconfont
import { Icon } from "ant-design-vue";
const IconFont = Icon.createFromIconfontCN({
  scriptUrl: "//at.alicdn.com/t/font_2259632_j8ib3ovpqh.js",
  extraCommonProps: {
    color: "#fff",
  },
});
// 指令
import clickoutside from "../../../../../plugins/directivesSet/clickoutside";
// 导入外部js
import imgSuccess from "../../../../../static/png/success.png";
import imgRunning from "../../../../../static/png/running.png";
import imgFailed from "../../../../../static/png/failed.png";

export default {
  name: "analysisList",

  props: {
    searchData: {
      type: Array,
    },
    listLoading: {
      type: Boolean,
    },
  },

  data() {
    return {
      loading: true,
      isOptions: false,
      isNoData: true,

      imgSuccess: imgSuccess, // 状态图片
      imgRunning: imgRunning,
      imgFailed: imgFailed,

      // 是否开启下拉analysis history菜单
      isAnaHis: null,
    };
  },

  computed: {
    ...mapState({
      navWorkflow: (state) => state.titleWorkflow.navWorkflow,
    }),
  },

  // 自定义指令
  directives: {
    clickoutside,
  },

  components: {
    IconFont,
  },

  mounted() {},

  methods: {
    // 打开面试
    openOptions(e) {
      this.isAnaHis = e;
      this.isOptions = !this.isOptions;
    },

    toResult(e, d) {
      this.workflowName = e.name;
      this.$store.commit("getWorkflowId", e.id);

      if (d == 1) {
        this.$emit("handleToWorkflow", e);
      } else if (d == 2) {
        this.$emit("handleToResult", e);
      }

      // 网页点击追踪
      let arr = [];
      arr.push(this.navWorkflow[0]);
      let obj_0 = {
        title: "Analysis",
        sign: 2,
      };
      arr.push(obj_0);
      let obj_1 = {
        title: e.project.name,
        sign: 3,
      };
      arr.push(obj_1);
      let obj_2 = {
        title: e.name,
        sign: 4,
      };
      arr.push(obj_2);
      this.$store.commit("postNavWorkflowData", arr);

      let obj = {
        id: e.project.id,
        name: e.project.name,
      };
      this.$store.commit("getProjectId", obj);
      sessionStorage.setItem("getProjectId", JSON.stringify(obj));
    },
    del(item) {
      console.log(item);
    },
    downLoad(item) {
      console.log(item);
      this.$ProjectModels
        .downloadResult(item.id, item.project.id)
        .then((res) => {
          const blob = new Blob([res.data], { type: "application/zip" }); // new一个二进制对象
          const url = window.URL.createObjectURL(blob); // 转化为url
          const link = document.createElement("a"); // 创建个a标签
          link.href = url;
          link.download = "workflow_" + item.name + "_" + item.id; // 重命名
          link.click();
          URL.revokeObjectURL(url);
        })
        .catch((error) => {
          this.$message.error(
            "Failed to update the data. Please try again or check the network." +
              error,
            5
          );
        });
    },
  },

  watch: {
    listLoading(a) {
      this.loading = a;
    },

    isOptions() {
      if (!this.isOptions) {
        this.isAnaHis = null;
      }
    },
  },
};
</script>

