/*
 * @componmentName resize
 * @module "./resize.js"
 * @fileDescription vue的自定义指令集，功能：监听页面元素的宽高变化。 
 * @author li
 * @date 2020年8月20日
 * @example 调用示例
 * 使用import引入之后，在vue自定义指令集注册即可
 * import resize from './resize';
 * directives: {
 *   resize
 * }, 
 * // v-resize 接受一个参数
 * <div v-resize="resize"></div>
 * //在methods中
 * resize() {  // 当宽高变化时就会执行
 *  //执行某些操作
 * }
 */

const resize = { // 指令的名称
    // el为绑定的元素，binding为绑定给指令的对象
    bind(el, binding) {
        let width = '',
            height = '';

        function isReize() {
            // 获取到document文档中的元素样式
            const style = document.defaultView.getComputedStyle(el); 
            if (width !== style.width || height !== style.height) {
                // 关键 检查变化
                binding.value({width, height});
            }
            width = style.width;
            height = style.height;
        }
        // 使用定时器，防止一直请求页面元素的宽高
        el.__vueSetInterval__ = setInterval(isReize, 300); 
    },
    unbind(el) {
        clearInterval(el.__vueSetInterval__);
    }
};

export default resize; // 监听页面元素的宽高变化