export default {
    methods: {
        // 鼠标按下
        onMousedown(event) {
            // 只有鼠标先按下，才开启事件
            // 判断节点是点击还是移动
            this.isMove = false;

            /*****  连线逻辑判断  *****/
            // 获取输入输出的坐标数据
            let arr = [...this.outputPointCoordinates];
            console.log(arr);
            // 进行判断
            arr.forEach((element) => {
                if (
                    element.pointCoordinates[0].x * this.zoom < event.offsetX &&
                    event.offsetX < (element.pointCoordinates[0].x + element.pointCoordinates[0].sideChief) * this.zoom &&
                    element.pointCoordinates[0].y * this.zoom < event.offsetY &&
                    event.offsetY < (element.pointCoordinates[0].y + element.pointCoordinates[0].sideChief) * this.zoom
                ) {
                    // 获取到点击的输入或者输出点坐标
                    this.outputPoint = {
                        x: element.pointCoordinates[0].x + element.pointCoordinates[0].sideChief,
                        y: element.pointCoordinates[0].y + element.pointCoordinates[0].sideChief / 2,
                        output: element.output
                    };
                    this.isLine = true;
                }
            });
            if (!this.isLine) {
                /*****  画布移动初始数据记录  *****/
                this.canvasNodeX = event.clientX - this.canvasX;
                this.canvasNodeY = event.clientY - this.canvasY;
                this.isCanvas = true;

                /*****  获取鼠标具体位置，判断是否在节点位置中  *****/
                let reverseArray = [...this.nodesListData];
                this.isNodeMove = reverseArray.reverse().find(ele => {
                    return (ele.x * this.zoom < event.offsetX && event.offsetX < (ele.x + ele.width) * this.zoom && ele.y * this.zoom < event.offsetY && event.offsetY < (ele.y + ele.height) * this.zoom);
                });
                this.nodeCoordinate = {
                    x: event.clientX,
                    y: event.clientY
                };

            }
        },

        //鼠标抬起
        onMouseup(event) {
            if (this.isLine) {
                /*****  判断鼠标落点位置，只有当鼠标停止位置为规定输入点位置时候才可以连线，否则清空连线数据  *****/
                let arr = [...this.inputPointCoordinates];

                // 进行判断
                arr.forEach((element) => {
                    // 鼠标抬起位置在输入点上
                    if (
                        element.pointCoordinates[0].x * this.zoom < event.offsetX &&
                        event.offsetX < (element.pointCoordinates[0].x + element.pointCoordinates[0].sideChief) * this.zoom &&
                        element.pointCoordinates[0].y * this.zoom < event.offsetY &&
                        event.offsetY < (element.pointCoordinates[0].y + element.pointCoordinates[0].sideChief) * this.zoom
                    ) {
                        // 添加路径之前先要判断是否存在重复路径
                        // 添加路径信息
                        if (!this.pathsListData.some(ele => {
                                return JSON.stringify(ele) === JSON.stringify({
                                    input: element.input[0].id,
                                    output: this.outputPoint.output[0].id
                                });
                            })) {
                            this.pathsListData.push({
                                input: element.input[0].id,
                                output: this.outputPoint.output[0].id
                            });
                            this.$emit("changePathData", this.pathsListData);
                        }

                    }

                    // 清空画布
                    this.cxt.clearRect(0, 0, this.canvasElement.width, this.canvasElement.height);
                    // 绘制背景
                    this.drawGird();

                    /*****  绘制节点 *****/
                    this.nodesListData.forEach((ele) => {
                        // 启动绘制函数
                        this.fillRoundRect(this.cxt, ele.x * this.zoom, ele.y * this.zoom, ele.width, ele.height, ele.radius, "#fff", "#e4e4e4", ele.color, ele.allName, ele.fontSize);
                    });
                    /*****  绘制线段  *****/
                    this.pathsListData.forEach((inx) => {
                        // 获取输入点坐标数据
                        let inCoordinate_ING = this.inputPointCoordinates.find(a => {
                            return inx.input == a.input[0].id;
                        });
                        // 获取输出点坐标数据
                        let outCoordinate_ING = this.outputPointCoordinates.find(a => {
                            return inx.output == a.output[0].id;
                        });
                        // 绘制
                        this.drawArrow((outCoordinate_ING.pointCoordinates[0].x + this.fontSize) * this.zoom, (outCoordinate_ING.pointCoordinates[0].y + this.fontSize / 2) * this.zoom, inCoordinate_ING.pointCoordinates[0].x * this.zoom, (inCoordinate_ING.pointCoordinates[0].y + 4) * this.zoom);
                    });

                });
                this.isLine = false;
                return;
            } else {
                this.isCanvas = false;

                if (this.isNodeMove && this.isMove) {
                    /*****  对节点坐标数据重新记录  *****/
                    this.nodesListData.forEach((ele) => {
                        if (this.isNodeMove === ele) {
                            ele.x = this.nodeCoordinate_ING.x;
                            ele.y = this.nodeCoordinate_ING.y;
                        }
                    });

                    this.$emit("changeNodeData", this.nodesListData);

                    /*****  对移动后的输入点坐标数据重新记录  *****/
                    // 计算输入点坐标，（暂时为一个，所以不做循环，直接写死）
                    let inCoordinate_ING = [{
                        x: this.nodeCoordinate_ING.x + 10,
                        y: this.nodeCoordinate_ING.y + 50,
                        sideChief: this.fontSize * this.zoom
                    }, ];

                    this.inputPointCoordinates.forEach((ele) => {
                        if (this.isNodeMove.id === ele.id) {
                            ele.pointCoordinates = inCoordinate_ING;
                        }
                    });

                    /*****  对移动后的输出点坐标数据重新记录  *****/
                    // 根据标题长度计算整体长度;
                    let textWidth =
                        this.cxt.measureText(this.isNodeMove.name).width + 60 < 170 ?
                        170 :
                        this.cxt.measureText(this.isNodeMove.name).width + 80;
                    let scaleWidth = textWidth;
                    // 计算输入点坐标，（暂时为一个，所以不做循环，直接写死）
                    let outCoordinate_ING = [{
                        x: this.nodeCoordinate_ING.x + scaleWidth - 26,
                        y: this.nodeCoordinate_ING.y + 80,
                        sideChief: this.fontSize * this.zoom,
                    }, ];

                    this.outputPointCoordinates.forEach((ele) => {
                        if (this.isNodeMove.id === ele.id) {
                            ele.pointCoordinates = outCoordinate_ING;
                        }
                    });
                }

                /*****  禁止移动  *****/
                this.isNodeMove = false;
            }
        },

        onMouseout() {
            this.isCanvas = false;
        },

        //鼠标移动
        onMousemove(event) {
            // 禁止浏览器默认事件
            event.preventDefault();

            // 判断节点是点击还是移动
            this.isMove = true;

            // 获取动画
            window.requestAnimationFrame = window.requestAnimationFrame || window.mozRequestAnimationFrame || window.webkitRequestAnimationFrame || window.msRequestAnimationFrame;

            // 更改鼠标样式，当鼠标到达输入输出时，样式更改
            let readMousePositionLine = () => {

                /*****  获取鼠标具体位置，判断是否在节点位置中  *****/
                let reverseArray = [...this.nodesListData];

                if (reverseArray.reverse().find(ele => {
                        return (ele.x * this.zoom < event.offsetX && event.offsetX < (ele.x + ele.width) * this.zoom && ele.y * this.zoom < event.offsetY && event.offsetY < (ele.y + ele.height) * this.zoom);
                    })) {

                    /*****  获取鼠标具体位置，判断是否在输入输出位置中  *****/
                    // 获取输入输出的坐标数据
                    let arr = [...this.outputPointCoordinates];
                    let bo = arr.some(element => {
                        return element.pointCoordinates[0].x * this.zoom < event.offsetX &&
                            event.offsetX < (element.pointCoordinates[0].x + element.pointCoordinates[0].sideChief) * this.zoom &&
                            element.pointCoordinates[0].y * this.zoom < event.offsetY &&
                            event.offsetY < (element.pointCoordinates[0].y + element.pointCoordinates[0].sideChief) * this.zoom;
                    });
                    if (bo) {
                        // 更改样式
                        this.classStyleL = 'lineStyle';
                        this.classStyleP = '';
                    } else {
                        this.classStyleL = '';
                        this.classStyleP = 'nodeStyle';
                    }
                } else {
                    this.classStyleP = '';
                }
            };
            window.requestAnimationFrame(readMousePositionLine);


            // 判断是否开始连线
            if (this.isLine) {
                // 清空画布
                this.cxt.clearRect(0, 0, this.canvasElement.width, this.canvasElement.height);
                // 绘制背景
                this.drawGird();
                // 开始绘制，循环绘制

                /*****  绘制节点  *****/
                this.nodesListData.forEach((ele) => {
                    // 启动绘制函数
                    this.fillRoundRect(this.cxt, ele.x * this.zoom, ele.y * this.zoom, ele.width, ele.height, ele.radius, "#fff", "#e4e4e4", ele.color, ele.allName, ele.fontSize);
                });

                /*****  绘制线段  *****/
                // 已存在的线段要绘制
                this.pathsListData.forEach((inx) => {
                    // 获取输入点坐标数据
                    let inCoordinate_ING = this.inputPointCoordinates.find(a => {
                        return inx.input == a.input[0].id;
                    });
                    // 获取输出点坐标数据
                    let outCoordinate_ING = this.outputPointCoordinates.find(a => {
                        return inx.output == a.output[0].id;
                    });
                    // 绘制
                    this.drawArrow((outCoordinate_ING.pointCoordinates[0].x + this.fontSize) * this.zoom, (outCoordinate_ING.pointCoordinates[0].y + this.fontSize / 2) * this.zoom, inCoordinate_ING.pointCoordinates[0].x * this.zoom, (inCoordinate_ING.pointCoordinates[0].y + 4) * this.zoom);
                });

                /*****  绘制线段  *****/
                // 正在绘制的线段
                this.drawArrow(this.outputPoint.x, this.outputPoint.y, event.offsetX, event.offsetY);
            } else {
                // 判断是否在移动节点
                if (!!this.isNodeMove && this.isCanvas) {

                    // 清空画布
                    this.cxt.clearRect(0, 0, this.canvasElement.width, this.canvasElement.height);
                    // 绘制背景
                    this.drawGird();
                    // 开始绘制，循环绘制

                    // 绘制节点
                    this.nodesListData.forEach((ele) => {

                        /*****  绘制节点  *****/
                        // 判断是否是选中的节点数据，是的话进行移动绘制
                        if (this.isNodeMove === ele) {
                            // 计算绘制的X、Y偏移量
                            let changeX = event.clientX - this.nodeCoordinate.x;
                            let changeY = event.clientY - this.nodeCoordinate.y;
                            // 记录绘制之后的坐标
                            this.nodeCoordinate_ING = {
                                x: (ele.x * this.zoom + changeX) / this.zoom,
                                y: (ele.y * this.zoom + changeY) / this.zoom,
                            };

                            // 启动绘制函数
                            this.fillRoundRect(
                                this.cxt,
                                (ele.x * this.zoom + changeX),
                                (ele.y * this.zoom + changeY),
                                ele.width,
                                ele.height,
                                ele.radius,
                                /*optional*/
                                "#fff",
                                /*optional*/
                                "#e4e4e4",
                                ele.color,
                                ele.allName,
                                ele.fontSize
                            );

                            // 将重新移动后的节点数据进行重新记录
                            /*****  对移动后的输入点坐标数据重新记录  *****/
                            // 计算输入点坐标，（暂时为一个，所以不做循环，直接写死）
                            let inCoordinate_ING = [{
                                x: this.nodeCoordinate_ING.x + 10,
                                y: this.nodeCoordinate_ING.y + 50,
                                sideChief: this.fontSize * this.zoom
                            }, ];

                            this.inputPointCoordinates.forEach((ele) => {
                                if (this.isNodeMove.id === ele.id) {
                                    ele.pointCoordinates = inCoordinate_ING;
                                }
                            });

                            /*****  对移动后的输出点坐标数据重新记录  *****/
                            // 根据标题长度计算整体长度;
                            let textWidth = this.cxt.measureText(this.isNodeMove.name).width + 60 < 170 ? 170 : this.cxt.measureText(this.isNodeMove.name).width + 80;
                            let scaleWidth = textWidth;

                            // 计算输入点坐标，（暂时为一个，所以不做循环，直接写死）
                            let outCoordinate_ING = [{
                                x: this.nodeCoordinate_ING.x + scaleWidth - 26,
                                y: this.nodeCoordinate_ING.y + 80,
                                sideChief: this.fontSize * this.zoom,
                            }, ];

                            this.outputPointCoordinates.forEach((ele) => {
                                if (this.isNodeMove.id === ele.id) {
                                    ele.pointCoordinates = outCoordinate_ING;
                                }
                            });
                        } else {
                            /*****  绘制节点  *****/
                            // 没有移动的，则按照原坐标进行绘制
                            this.fillRoundRect(this.cxt, ele.x * this.zoom, ele.y * this.zoom, ele.width, ele.height, ele.radius, "#fff", "#e4e4e4", ele.color, ele.allName, ele.fontSize);
                        }

                    });

                    // 绘制线段
                    this.pathsListData.forEach((inx) => {
                        let inCoordinate_ING = this.inputPointCoordinates.find(a => {
                            return inx.input == a.input[0].id;
                        });
                        let outCoordinate_ING = this.outputPointCoordinates.find(a => {
                            return inx.output == a.output[0].id;
                        });
                        this.drawArrow((outCoordinate_ING.pointCoordinates[0].x + this.fontSize) * this.zoom, (outCoordinate_ING.pointCoordinates[0].y + this.fontSize / 2) * this.zoom, inCoordinate_ING.pointCoordinates[0].x * this.zoom, (inCoordinate_ING.pointCoordinates[0].y + 4) * this.zoom);
                    });
                }

                // 判断是否在移动画布整体 
                else if (this.isCanvas) {
                    let readMousePosition = () => {
                        // 移动画布整体，需要获取元素
                        let parentElement = document.getElementById("canvas");
                        let style = document.defaultView.getComputedStyle(parentElement, null);
                        // 获取移动边界
                        let maxWidth = style.width;
                        let maxHeight = style.height;
                        if (3000 - parseInt(maxWidth) < -(event.clientX - this.canvasNodeX) || event.clientX - this.canvasNodeX > 0 || event.clientY - this.canvasNodeY > 0 || 2500 - parseInt(maxHeight) < -(event.clientY - this.canvasNodeY)) {
                            return;
                        }
                        // 添加移动偏移量
                        this.canvasX = event.clientX - this.canvasNodeX;
                        this.canvasY = event.clientY - this.canvasNodeY;
                    };
                    window.requestAnimationFrame(readMousePosition);
                }
            }

        },


        // 节点移动后输入输出点的记录
        recordNode() {

        }
    },
};