<template>
  <a-modal
    title="library"
    :visible="libraryTable"
    :dataSource="librarys"
    @ok="handleOk"
    @cancel="handleCancel"
    destroyOnClose
    width="60%"
  >
    <div class="body">
      <a-table
        :columns="columns"
        :dataSource="librarys"
        :row-key="(r, i) => i.toString()"
        size="middle"
        :row-selection="{
          selections: selections,
          onChange: onSelectChange,
        }"
      >
        <template
          slot="title"
          style="background-color: #dfe9f5"
        >
          <a-button @click="back">
            <a-icon type="arrow-left" />
          </a-button>
          <a-button
            style="margin-left:1rem"
            @click="uploadFile"
          >
            <a-icon type="to-top" />
          </a-button>
        </template>
        <div
          slot="id"
          slot-scope="text, record"
        >
          <div
            style="width: 1.5rem; height: 1.5rem"
            v-if="record.hasOwnProperty('id')"
          >
            <img
              style="width: 100%; height: 100%"
              src="@/assets/icon/文件夹.png"
            />
          </div>
          <div
            style="width: 1.2rem; height: 1.2rem"
            v-if="record.hasOwnProperty('size')"
          >
            <img
              style="width: 100%; height: 100%"
              :src="fileType(record.name.split('.').pop().toLowerCase())"
            />
          </div>
        </div>
        <div
          slot="name"
          slot-scope="text, record"
        >
          <span @click="showFolder(record)"><a>{{ record.name }}</a></span>
        </div>
        <div
          slot="size"
          slot-scope="text, record"
          v-if="record.hasOwnProperty('size')"
        >
          <span>{{ unitConversion(record.size) }}</span>
        </div>
      </a-table>
    </div>
    <uploadFileVue
      :libraryid="libraryid"
      @uploadlibrarys="uploadlibrarys"
    />
  </a-modal>
</template>

<script>
import { mapState } from "vuex";
import { fileType } from "@/utils/icon.js";
import { unitConversion } from "@/utils/utils.js";
import uploadFileVue from "./uploadFile.vue";
const columns = [
  {
    dataIndex: "id",
    key: "id",
    title: "",
    scopedSlots: { customRender: "id" },
  },
  {
    dataIndex: "name",
    key: "name",
    title: "名称",
    sorter: true,
    scopedSlots: {
      customRender: "name",
      filterDropdown: "filterDropdown",
      filterIcon: "filterIcon",
    },
  },
  {
    dataIndex: "update_time",
    key: "update_time",
    title: "更新时间",
    sorter: true,
  },
  {
    dataIndex: "size",
    key: "size",
    title: "文件大小",
    sorter: true,
    scopedSlots: { customRender: "size" },
  },
];
export default {
  name: "LibraryTable",
  components: {
    uploadFileVue
  },
  props: {
    name: String,
  },
  data() {
    return {
      columns,
      selections: [],
      librarys: [],
      libraryid: "",
    };
  },
  computed: {
    ...mapState({
      libraryTable: (state) => state.folder.libraryTable,
    }),
  },
  watch: {
    name: {
      handler(n, o) {
        console.log(n);
      },
    },
  },
  methods: {
    fileType,
    unitConversion,
    handleOk() {
      if (this.name == "MAF") {
        this.$emit("MAFselections", this.selections, this.libraryid);
      } else if (this.name == "VCF") {
        this.$emit("VCFselections", this.selections, this.libraryid);
      } else if (this.name == "CNV") {
        this.$emit("CNFselections", this.selections, this.libraryid);
      } else if (this.name == "matrix") {
        this.$emit("Matrixselections", this.selections, this.libraryid);
      } else if (this.name == "matrixMetabolome") {
        this.$emit(
          "MatrixMetabolomeselections",
          this.selections,
          this.libraryid
        );
      } else if (this.name == "DimensionReduction") {
        this.$emit(
          "DimensionReductionselections",
          this.selections,
          this.libraryid
        );
      } else if (this.name == "OPLSPLS") {
        this.$emit("OPLSPLSselections", this.selections, this.libraryid);
      } else if (this.name == 'RNA-Proteome') {
        this.$emit("RNAselections", this.selections, this.libraryid)
      } else if (this.name == 'SNV') {
        this.$emit("SNVselections", this.selections, this.libraryid)
      } else if (this.name == 'RNAseq') {
        this.$emit("RNAseqselections", this.selections, this.libraryid)
      } else if (this.name == 'Metabolome') {
        this.$emit("Metabolomeselections", this.selections, this.libraryid)
      } else if (this.name == 'survival') {
        this.$emit("survivalselections", this.selections, this.libraryid)
      }
      this.$store.commit("changelibraryTable", false);
    },
    handleCancel() {
      this.$store.commit("changelibraryTable", false);
    },
    onSelectChange(selectedRowKeys, selections) {
      console.log("selectedRowKeys changed: ", selections);
      this.selections = selections;
    },
    getAllLibrary() {
      this.$ProjectModels.allLibrary().then((res) => {
        this.librarys = res.data;
      });
    },
    showFolder(e) {
      this.libraryid = e.id;
      this.$ProjectModels.getFolder(e.id).then((res) => {
        this.librarys = res.data;
      });
    },
    back() {
      this.$ProjectModels.allLibrary().then((res) => {
        this.librarys = res.data;
      });
    },
    uploadFile() {
      this.$store.commit("changeUploadFiles", true);
    },
    //上传文件后刷新表格
    uploadlibrarys(e) {
      this.librarys = e;
    },
  },
  created() {
    this.libraryid = "";
  },
  mounted() {
    this.getAllLibrary();
  },
  beforeCreate() { },
  beforeMount() { },
  beforeUpdate() { },
  updated() { },
  beforeDestroy() { },
  destroyed() { },
  activated() { },
};
</script>
<style lang="less" scoped>
.body {
  overflow: auto;
  width: 100%;
  height: 30rem;
}
</style>