<template>
  <div class="FunctionalList">
    <div class="title">FAGL</div>
       
    <a-divider style="margin:0.625rem 0;" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin:0.625rem 0;" />
    <div class="data-taxonomy">
      <div class="title-second">Taxonomy:</div>
      <div class="content-data">
        <a-radio-group @change="onChange_0" v-model="value">
          <a-radio :value="1">human</a-radio>
          <a-radio :value="2">mouse</a-radio>
        </a-radio-group>
      </div>
    </div>
  
    <a-divider dashed style="margin:0.625rem 0;" />
    
    <!-- 复选框三个GO kEGG reactome -->
    <div class="title-second">Annotation database:</div>
    <div class="content-data">
      <div :style="{ borderBottom: '0.0625rem solid #E9E9E9' }">
        <a-checkbox :indeterminate="indeterminate" @change="onCheckAllChange" :checked="checkAll">
          Check all
        </a-checkbox>
      </div>
      <br />
      <a-checkbox-group :options="plainOptions" v-model="checkedList" @change="onChange" style='display: flex;flex-direction: column;' />
    </div>

  </div>
</template>

<script>
const plainOptions = ['GOBP', 'KEGG', 'Reactome']
import { mapState } from 'vuex';
import changeName from "./components/changeName";

export default {
  name: "FunctionalList",
  
  props:{
    nodeId:{
      type:Number
    }
  },

  data(){
    return{
      params:{
        data:{},
      },
      checkedList:[],
      indeterminate: true,
      checkAll: false,
      plainOptions,
      value:1,
      taxonomy:'human'
    }
  },

  computed: {
    ...mapState({
      projectId: state => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: state => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    })
  },
    
  components:{
    changeName
  },

  mounted(){
    this.recoverData(this.nodeId)
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    this.params.GOBP= false;
    this.params.KEGG= false;
    this.params.Reactome= false;
    this.checkedList.forEach(res =>{
      if(res=="GOBP"){
        this.params.GOBP = true
      }
      if(res=="KEGG"){
        this.params.KEGG = true
      }
      if(res=="Reactome"){
        this.params.Reactome = true
      }      
    })
    if (this.value == 1) {
      this.params.taxonomy = 'human';
    } else {
      this.params.taxonomy = 'mouse';
    }
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach(element => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit('postworkflowNodeData', arr);
  },

  methods: {
    onChange (checkedList) {
      this.indeterminate = !!checkedList.length && (checkedList.length < plainOptions.length)
      this.checkAll = checkedList.length === plainOptions.length
    },

    onCheckAllChange (e) {
      // console.log(e)
      Object.assign(this, {
        checkedList: e.target.checked ? plainOptions : [],
        indeterminate: false,
        checkAll: e.target.checked,
      })
    },
    
    onChange_0(e){
      if (e.target.value == 1) {
        this.taxonomy = 'human';
      } else {
        this.taxonomy = 'mouse';
      }
    },

    // 复用函数，复原数据
    recoverData(a){
      var that = this
      that.workflowNodeInfo.forEach(element => {
        if(element.id == a && JSON.stringify(element.params) !== '{}'){
          if (element.params.taxonomy == 'human') {
            this.value = 1;
          } else {
            this.value = 2;
          }
          if(element.params.GOBP == true){
            this.checkedList.push('GOBP')
          }
          if(element.params.KEGG == true){
            this.checkedList.push("KEGG")
          }
          if(element.params.Reactome == true){
            this.checkedList.push("Reactome")
          }      
        }
        if(element.id == a && JSON.stringify(element.params) == '{}'){
          this.value = 1
          this.checkedList = []
        }
      });
    }
  },
};
</script>

<style lang="scss" scoped>
.FunctionalList {
  padding: 0rem 1.25rem 1.875rem 1.25rem;
  .title {
    font-weight: 600;
    font-style: italic;
  }
  .title-second {
    font-weight: 550;
    margin: 0.9375rem 0 0.9375rem 0;
  }
  .title-third {
    font-size: 0.75rem;
    margin: 0.9375rem 0 0.3125rem 0;
  }
  .content-data {
    padding: 0rem 0rem 0.625rem 0.9375rem;
  }
  .data-transformation{
    padding: 0rem 0rem 0.625rem 0.9375rem;
  }
}
</style>