<template>
  <div class="VolcanoPlot">
    <div class="title">Volcano Plot</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin: 0.625rem 0" />
    <div class="points-color">
      <div class="title-second">points color :</div>
      <div class="content-data">
        <div class="title-third">Left color:</div>
        <a-input
          placeholder="Left Color"
          v-model="leftColor"
          ref="leftColor"
          size="small"
        >
          <a-icon slot="prefix" type="right-square" @click="getleftcolor" />
          <a-icon
            v-if="leftColor"
            slot="suffix"
            type="close-circle"
            @click="emitEmptyLeft"
          />
        </a-input>
        <div v-show="leftcolor">
          <sketch-picker
            v-model="color"
            @input="colorValueChange1"
          ></sketch-picker>
        </div>
        <div class="title-third">Right color:</div>
        <a-input
          placeholder="Right Color"
          v-model="rightColor"
          ref="rightColor"
          size="small"
        >
          <a-icon slot="prefix" type="right-square" @click="getrightcolor" />
          <a-icon
            v-if="rightColor"
            slot="suffix"
            type="close-circle"
            @click="emitEmptyRight"
          />
        </a-input>
        <div v-show="rightcolor">
          <sketch-picker
            v-model="color"
            @input="colorValueChange2"
          ></sketch-picker>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";
import { Sketch } from "vue-color";

export default {
  name: "VolcanoPlot",

  props: {
    nodeId: {
      type: Number,
    },
  },

  data() {
    return {
      leftColor: "",
      rightColor: "",
      params: {},
      test: "",
      color: "",
      leftcolor: false,
      rightcolor: false,
    };
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },

  components: {
    changeName,
    "sketch-picker": Sketch,
  },

  mounted() {
    this.recoverData(this.nodeId);
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex;
    this.params.leftColor = this.leftColor;
    this.params.rightColor = this.rightColor;
    let obj = Object.assign({}, this.params);
    let arr = Array.from(this.workflowNodeInfo);
    arr.forEach((element) => {
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },

  methods: {
    getrightcolor() {
      if (this.rightcolor == true) {
        this.rightcolor = false;
      } else {
        this.rightcolor = true;
      }
    },
    getleftcolor() {
      if (this.leftcolor == true) {
        this.leftcolor = false;
      } else {
        this.leftcolor = true;
      }
    },
    // 颜色输入框点击事件处理
    colorInputClick() {
      this.isShowColors = !this.isShowColors;
    },
    // 颜色值改变事件处理
    colorValueChange1(val) {
      window.console.log(val);

      // 取颜色对象的十六进制值
      this.leftColor = val.hex;
    },
    colorValueChange2(val) {
      window.console.log(val);

      // 取颜色对象的十六进制值
      this.rightColor = val.hex;
    },

    //输入框清空
    emitEmptyLeft() {
      this.$refs.leftColor.focus();
      this.leftColor = "";
    },
    //输入框清空
    emitEmptyRight() {
      this.$refs.rightColor.focus();
      this.rightColor = "";
    },

    // 复用函数，复原数据
    recoverData(a) {
      this.workflowNodeInfo.forEach((element) => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {
            this.leftColor = element.params.leftColor;
            this.rightColor = element.params.rightColor;
          }
        }
      });
    },
  },
};
</script>

