<template>
  <div class="Aggregation">
    <div class="title">Aggregation</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin: 0.625rem 0" />
    <div class="title-second">Aggregation:</div>

    <div class="content-data">
      <div
        class="anticon-tag-condition"
        v-for="(item, key) in aggregation"
        :key="key"
      >
        <div
          class="condition-tag"
          :style="{ background: '#5F7EC6', color: '#fff' }"
        >
          <a @click="removedAllTag(item)">
            <i class="anticon anticon-close">
              <svg
                viewBox="64 64 896 896"
                data-icon="close"
                width="1em"
                height="1em"
                fill="currentColor"
                aria-hidden="true"
                class
              >
                <path
                  d="M563.8 512l262.5-312.9c4.4-5.2.7-13.1-6.1-13.1h-79.8c-4.7 0-9.2 2.1-12.3 5.7L511.6 449.8 295.1 191.7c-3-3.6-7.5-5.7-12.3-5.7H203c-6.8 0-10.5 7.9-6.1 13.1L459.4 512 196.9 824.9A7.95 7.95 0 0 0 203 838h79.8c4.7 0 9.2-2.1 12.3-5.7l216.5-258.1 216.5 258.1c3 3.6 7.5 5.7 12.3 5.7h79.8c6.8 0 10.5-7.9 6.1-13.1L563.8 512z"
                />
              </svg>
            </i>
          </a>
          {{ item.dimension }} / {{ item.function }}
        </div>
        <div style="text-align: center">
          <img
            src="../../../../../../assets/png/down.png"
            style="height: 20px; width: 20px"
          />
        </div>
      </div>
      <a-cascader
        size="small"
        :options="options"
        @change="onChange"
        style="margin-top: 0.625rem"
      ></a-cascader>
    </div>

    <a-divider dashed style="margin: 0.625rem 0" />
    <div class="title-second">Paired Ratio:</div>
    <div class="content-data">
      <div class="row">
        <p class="title-third ">dimension: row</p> 
        <form action="/" method="post" id="uploadForm" enctype="multipart/form-data">
          <div class="file-box" style="cursor: pointer;">
            Up File
            <input
              type="file"
              id="files"
              class="file-btn"
              name="files"
              placeholder="file"
              multiple
              @change="tirggerFile_r($event)"
            />
          </div>
        </form>
        <ul class="upload-list">
          <li  v-for="(file, index) in pairedRatio_r"  :key="index" class="upload-list-li">
            <a class="upload-list-li-name">
              <i class="fa fa-file-text-o" aria-hidden="true"></i>
              {{ file.filename }}
            </a>
            <label class="upload-list-li-label" @click="deleFile_r(index)"> 
              <i style="cursor: pointer;">delete</i>
            </label>
          </li>
        </ul>
      </div>

      <div class="column">
        <p class="title-third ">dimension: column</p> 
        <form action="/" method="post" id="uploadForm" enctype="multipart/form-data">
          <div class="file-box" style="cursor: pointer;">
            Up File
            <input
              type="file"
              id="files"
              class="file-btn"
              name="files"
              placeholder="file"
              multiple
              @change="tirggerFile_c($event)"
            />
          </div>
        </form>
        <ul class="upload-list">
          <li  v-for="(file, index) in pairedRatio_c"  :key="index" class="upload-list-li">
            <a class="upload-list-li-name">
              <i class="fa fa-file-text-o" aria-hidden="true"></i>
              {{ file.filename }}
            </a>
            <label class="upload-list-li-label" @click="deleFile_c(index)"> 
              <i style="cursor: pointer;">delete</i>
            </label>
          </li>
        </ul>
      </div>
      
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";

export default {
  name: "Aggregation",

  props: {
    nodeId: {
      type: Number,
    },
  },

  data() {
    return {
      params: {},
      // 自定义节点名称
      nodeName: "",
      // 是否开启修改
      inputNameShow: false,
      // 选择的数据收集
      aggregation: [],
      // 维度筛选数据集
      options: [
        {
          value: "row",
          label: "row",
          children: [
            {
              value: "mean",
              label: "mean",
            },
            {
              value: "median",
              label: "median",
            },
            {
              value: "sum",
              label: "sum",
            },
            {
              value: "max",
              label: "max",
            },
            {
              value: "min",
              label: "min",
            },
          ],
        },
        {
          value: "column",
          label: "column",
          children: [
            {
              value: "mean",
              label: "mean",
            },
            {
              value: "median",
              label: "median",
            },
            {
              value: "sum",
              label: "sum",
            },
            {
              value: "max",
              label: "max",
            },
            {
              value: "min",
              label: "min",
            },
          ],
        },
      ],

      // 上传文件收集文件名
      pairedRatio_c: [],
      pairedRatio_r: [],
      // 文件名
      fileList_c: [ ],
      // 文件名
      fileList_r: [ ],
    };
  },

  computed: {
    ...mapState({
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },

  components: {
    changeName,
  },

  mounted() {
    this.recoverData(this.nodeId);
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    this.params.aggregation = this.aggregation;
    this.params.pairedRatio = [...this.pairedRatio_c,...this.pairedRatio_r];
    this.params.fileList = [...this.fileList_c,...this.fileList_r]
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach((element) => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },

  methods: {
    onChange(value) {
      this.aggregation.push({
        dimension: value[0],
        function: value[1],
      });
    },

    // removedAllTag 移除当列所有筛选条件，n 为当前列的name
    removedAllTag(n) {
      // 删除计算机读取条件
      let index = this.aggregation.indexOf(n);
      this.aggregation.splice(index, 1);
    },

    tirggerFile_r(event) {
      var _this = this;
      var list = event.target.files;
      for (var i = 0; i < list.length; i++) {
        if (_this.fileList_r.length >= 5) {
          _this.common.showAlert("最多上传5个文件！");
          return false;
        } else {
          _this.fileList_r.push(list[i]);
          this.pairedRatio_r.push({
            dimension:"row",
            filename:list[i].name
          })
        }
      }
    },
    deleFile_r(i) {
      this.fileList_r.splice(i, 1);
      this.pairedRatio_r.splice(i, 1);
    },

    tirggerFile_c(event) {
      var _this = this;
      var list = event.target.files;
      for (var i = 0; i < list.length; i++) {
        if (_this.fileList_c.length >= 5) {
          _this.common.showAlert("最多上传5个文件！");
          return false;
        } else {
          _this.fileList_c.push(list[i]);
          this.pairedRatio_c.push({
            dimension:"column",
            filename:list[i].name
          })
        }
      }
    },
    deleFile_c(i) {
      this.fileList_c.splice(i, 1);
      this.pairedRatio_c.splice(i, 1);
    },

    uploadFil() {
      var _this = this;
      var formData = new formData();
      for (var i = 0; i < this.fileList.length; i++) {
        _this.formData.append("file", this.fileList[i]);
      }
    },

    // 复用函数，复原数据
    recoverData(a) {
      this.workflowNodeInfo.forEach((element) => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {
            this.aggregation = element.params.aggregation;
            element.params.pairedRatio.forEach(res =>{
              if(res.dimension == "row"){
                this.pairedRatio_r.push(res);
              }else{
                this.pairedRatio_c.push(res);
              }
            })
          }
        }
      });
    },
  },
};
</script>

