<template>
  <div class="CV">
    <div class="title">CV</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin: 0.625rem 0" />
    <div class="filter">
      <div class="title-second">instance</div>
      <div class="content-data">
        <a-radio-group
          v-model="params.cv"
          :options="options"
          @change="onChange"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";
const options = [
  { label: "row", value: "row" },
  { label: "column ", value: "column " },
];
export default {
  name: "CVcalculation",
  props: {
    nodeId: {
      type: Number,
    },
  },
  data() {
    return {
      options,
      params: {
        data: {},
        cv: "",
      },
    };
  },
  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    // this.params.MAFNumber = this.messageTags;
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach((element) => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },
  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },
  components: {
    changeName,
  },
  mounted() {
    this.recoverData(this.nodeId);
  },
  methods: {
    onChange(e) {
      this.params.instance = e.target.value;
    },
    recoverData(a) {
      this.workflowNodeInfo.forEach((element) => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {
            this.params.cv = element.params.cv;
          }
        }
      });
    },
  },
};
</script>

