<template>
  <div class="DMG">
    <div class="title">Differentially mutated genes</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin: 0.625rem 0" />
    <div class="filter">
      <div class="title-third">GroupA:</div>
      <a-select style="width: 100%" v-model="params.groupA" size="small">
        <a-select-option
          v-for="item in labels"
          :key="item.idx"
          :value="item.labelName"
          >{{ item.labelName }}</a-select-option
        >
      </a-select>
      <div class="title-third">GroupB:</div>
      <a-select style="width: 100%" v-model="params.groupB" size="small">
        <a-select-option
          v-for="item in labels"
          :key="item.idx"
          :value="item.labelName"
          >{{ item.labelName }}</a-select-option
        >
      </a-select>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";

export default {
  name: "DMG",
  props: {
    nodeId: {
      type: Number,
    },
  },
  data() {
    return {
      labels: [],
      params: {
        data: {},
        groupA: "",
        groupB: "",
      },
    };
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },
  components: {
    changeName,
  },
  methods: {
    getLabelData() {
      let id = this.projectId;
      //labelInfo
      this.$ProjectModels
        .getAllLabelModels(id)
        .then((res) => {
          this.labels = res.data.labels;
        })
        .catch((error) => window.console.error("Error:", error));
    },
  },
  mounted() {
    this.getLabelData();
  },
};
</script>

<style lang="scss" scoped>
.DMG {
  padding: 0rem 1.25rem 1.875rem 1.25rem;
  .title {
    font-weight: 600;
  }
  .title-second {
    font-size: 0.875rem;
    font-weight: 800;
    margin: 0.9375rem 0 0.3125rem 0;
  }
}
</style>