

<template>
  <div class="plot">
    <div class="venn">
      <div
        id="mountNode"
        class="mountNode"
        ref="mountNode"
      ></div>
      <div class="table">
        <div class="tablevalue">
          <p
            v-if="tableName != ''"
            style="font-weight: 800; font-size: 1rem; background-color: #ccc"
          >
            {{ tableName }}
          </p>
          <p
            :class="index % 2 == 0 ? 'ss1' : 'ss2'"
            v-for="(i, index) in tableData"
            :key="index"
          >
            {{ i }}
          </p>
        </div>
      </div>
    </div>
    <div class="slider">
      <div class="download">
        <div
          class="file-name"
          v-for="item in fileNames"
          :key="item.index"
        >
          <div class="btn">
            <div
              class="btn-download"
              @click="download(item)"
            >
              <a-button
                ghost
                icon="download"
                type="primary"
              >
                download
              </a-button>
            </div>
            <div
              class="btn-preview"
              @click="preview(item)"
            >
              <a-icon type="eye" />
            </div>
          </div>
          <div class="text-item">
            {{ item }}
          </div>
        </div>
      </div>

      <div class="sub-project">
        <div class="label-button">
          <h1>generate new quantification matrix</h1>
          <div
            class="line"
            style="width: 88%"
          ></div>
          <div class="cover-content">
            <!-- <div class="cover-label">
              <template v-for="(item, index) in labels">
                <a-tooltip
                  v-if="item.name.length > 20"
                  :key="index"
                  :title="item.name"
                >
                  <a-tag
                    :key="index"
                    closable
                    :afterClose="() => labelClose(item.name, item.labelId)"
                    :color="colors[index]"
                    >{{ `${item.name.slice(0, 20)}...` }}</a-tag
                  >
                </a-tooltip>
                <a-tag
                  v-else
                  :key="index"
                  closable
                  :afterClose="() => labelClose(item.name, item.labelId)"
                  :color="colors[index]"
                  >{{ item.name }}</a-tag
                >
              </template>
            </div> -->
          </div>
          <div>
            使用新的筛选后的数据添加新的任务
            <button
              class="run-task"
              @click.once="submitData"
            >running</button>
          </div>
        </div>
      </div>
    </div>
  </div>
