<script>
// antV组件

import { mapState } from "vuex";
// 颜色数组
import colors from "../../../../../utils/colors";
import state from "../../../../../store/state";

export default {
  name: "vennPlot",

  components: {},

  data() {
    return {
      Sets: [],
      // yanse
      colors: [],
      // 绘图数据
      plotData: {},
      tableName: "",
      tableData: [],
      secondData: [],
      fileNames: [],
      // 表格展示文件
      fileName: "result",
      // 是否显示添加label输入框
      labels: [],
    };
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      workflowId: (state) => state.workflowId.workflowId,
      resultNodeId: (state) => state.workflowId.resultNodeId,
      stateName: (state) => state.stateName.stateName,
    }),
  },

  created() {
    // 获取颜色
    this.colors = colors.colors;
    // 读取记录的ResultNodeId，以便渲染图
    this.$store.commit(
      "getResultNodeId",
      sessionStorage.getItem("getResultNodeId")
    );
  },

  mounted() {
    if (this.plotData.length == 0) {
      let id = this.projectId;
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      this.getPlotData(workflowId, id, nodeId, name);
    }
  },

  methods: {
    download(fileName) {
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      let id = this.projectId;
      let isFile = true;
      this.getTableFile(workflowId, id, nodeId, name, isFile, fileName);
    },

    preview(fileName) {
      this.fileName = fileName;
      this.getData();
    },
    getdata() {
      // eslint-disable-next-line no-undef
      var chart = new G2.Chart({
        container: "mountNode",
        forceFit: true,
        height: window.innerHeight,
        padding: 'auto'
      });
      chart.source(this.Sets);
      chart.legend({

        position: 'right-top',
        padding: 'auto'

      });
      chart.axis(false);
      chart.tooltip(true);
      chart
        .venn()
        .position("x*y")
        .sets("sets")
        .label("label", {
          textStyle: {
            fill: "white",
          },
        }) // 这个字段用来获取集合关系字段
        .size("size") // 这个字段用来获取集合 size
        .color("label")
        .active(false)
        .style({
          lineWidth: 10,
          padding: 10,
          textStyle: {
            fill: "white",
            textAlign: "center",
            fontSize: 32,
          },
        })
      chart.on("click", (ev) => {
        // 当用户在画布上点击时，这个时候用户看到tooltip上的数据其实已经被存在了hoverData里。拿到hoverData，相当于你拿到了折线上相关点的数据，之后再有什么需求都可以实现啦！
        this.tableName = ev.data.point.label.split(":")[0];
        for (let i in this.plotData) {
          if (i == ev.data.point.label.split(":")[0]) {
            this.tableData = this.plotData[i];
          }
        }
      });

      chart.render();
    },
    //遍历plotData的key值放进Sets中
    getVennData() {
      let arr1 = []
      let arr2 = []
      for (let i in this.plotData) {
        let list = {};

        if (i.indexOf("&") == -1) {
          list.sets = i.split("&");
          list.size = 12;
          list.label = i + ":" + this.plotData[i].length;
          arr1.push(list)
        } else {
          list.sets = i.split("&");
          list.size = 2;
          list.label = i + ":" + this.plotData[i].length;
          arr2.push(list)
        }


      }

      this.Sets = arr1.concat(arr2)
      this.Sets.sort(function (a, b) {
        console.log(a);
        return a.label.split('&').length - b.label.split('&').length
      })
      console.log(this.Sets);
      this.getdata();
    },
    // 复用函数 （图和表数据）
    getPlotData(workflowId, id, nodeId, name) {
      let isFile = false; // 请求图表的坐标数据，所以直接设置为false
      let ispng = false;
      this.$progress.start();
      this.$ProjectModels
        .getPlotResultModelsF(workflowId, id, nodeId, name, isFile, ispng)
        .then((val) => {
          this.Sets = [];
          this.$refs.mountNode.innerHTML = "";
          this.plotData = val.data;
          this.$store.commit("getStateName", name); // 储存点击的node name；
          this.$store.commit("pushCollects", this.plotData); //储存数据在vuex
          this.workflowName = val.data.name;
          if (val.data.length == 0) {
            this.$message.warning("No data");
          }
          this.$progress.finish();
        })
        .then((res) => {
          this.getVennData();
        });
    },

    // 父组件二次绘制任务收集
    secondDraw(e) {
      this.secondData = e;
    },
    getFileData() {
      let workflowId = this.workflowId;
      let nodeId = sessionStorage.getItem("getResultNodeId");
      let name = this.stateName;
      let id = this.projectId;
      this.$progress.start();
      this.$ProjectModels
        .getTaskNodeModels(workflowId, id, nodeId, name)
        .then((inx) => {
          this.fileNames = inx.data;
          if (inx.data.length == 0) {
            this.$message.warning("No task");
          }
          this.$progress.finish();
        });
    },

    // download表格文件
    getTableFile(workflowId, id, nodeId, name, isFile, fileName) {
      this.$progress.start();
      this.$ProjectModels
        .getTableResultModelsT(workflowId, id, nodeId, name, isFile, fileName)
        .then((ele) => {
          if (!ele) {
            return;
          }
          let url = window.URL.createObjectURL(ele.data); // 将对象转为一个url
          let link = document.createElement("a"); //创建一个a标签
          link.style.display = "none"; // 清除样式
          link.href = url; // 将
          link.setAttribute("download", fileName);
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);

          this.$progress.finish();
        });
    },
  },

  watch: {
    resultNodeId() {
      let id = this.projectId;
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      this.getPlotData(workflowId, id, nodeId, name);
      if (this.stateName != "FAGL") {
        this.getFileData();
      }
      this.labels = [];
    },
    workflowId() {
      let id = this.projectId;
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      this.getPlotData(workflowId, id, nodeId, name);
    },
  },
};
</script>

