<template>
  <div class="VennPlot">
    <div class="title">VennPlot</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin: 0.625rem 0" />
    <div class="filter">
      <div class="title-second">
        Choose <a-input class="label" v-model="params.labelValue" /> Labels:
        <span>Minimum 2 and maximum 5</span>
      </div>
    </div>
    <div class="title-third">
      <div class="title-third">GroupA:</div>
      <a-select style="width: 100%" v-model="params.groupA" size="small">
        <a-select-option
          v-for="item in GQMtoVannPlot"
          :key="item"
          :value="item"
          >{{ item }}</a-select-option
        >
      </a-select>
    </div>
    <div class="title-third">
      <div class="title-third">GroupB:</div>
      <a-select style="width: 100%" size="small" v-model="params.groupB">
        <a-select-option
          v-for="item in GQMtoVannPlot"
          :key="item"
          :value="item"
          >{{ item }}</a-select-option
        >
      </a-select>
    </div>
    <div
      class="title-third"
      v-show="params.labelValue >= 3 && params.labelValue <= 5"
    >
      <div class="title-third">GroupC:</div>
      <a-select style="width: 100%" size="small" v-model="params.groupC">
        <a-select-option
          v-for="item in GQMtoVannPlot"
          :key="item"
          :value="item"
          >{{ item }}</a-select-option
        >
      </a-select>
    </div>
    <div
      class="title-third"
      v-show="params.labelValue >= 4 && params.labelValue <= 5"
    >
      <div class="title-third">GroupD:</div>
      <a-select style="width: 100%" size="small" v-model="params.groupD">
        <a-select-option
          v-for="item in GQMtoVannPlot"
          :key="item"
          :value="item"
          >{{ item }}</a-select-option
        >
      </a-select>
    </div>
    <div class="title-third" v-show="params.labelValue == 5">
      <div class="title-third">GroupE:</div>
      <a-select style="width: 100%" size="small" v-model="params.groupE">
        <a-select-option
          v-for="item in GQMtoVannPlot"
          :key="item"
          :value="item"
          >{{ item }}</a-select-option
        >
      </a-select>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";

export default {
  name: "VennPlot",
  props: {
    nodeId: {
      type: Number,
      labelValues: Array,
    },
  },
  data() {
    return {
      // 用来储存请求的label数据
      labels: [],
      params: {
        data: {},
        labelValue: "2",
        groupA: "",
        groupB: "",
        groupC: "",
        groupD: "",
        groupE: "",
      },
    };
  },
  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    // this.params.MAFNumber = this.messageTags;
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach((element) => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },
  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
      GQMtoVannPlot: (state) => state.GQMtoVannPlot.labelName,
    }),
  },
  components: {
    changeName,
  },
  methods: {
    // 复用函数，请求label
    getLabelData() {
      let id = this.projectId;
      //labelInfo
      this.$ProjectModels
        .getAllLabelModels(id)
        .then((res) => {
          this.labels = res.data.labels;
        })
        .catch((error) => window.console.error("Error:", error));
    },
  },
  mounted() {
    this.getLabelData();
  },
};
</script>

