<template>
  <div class="SVM">
    <div class="title">Support Vector Machine</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <div class="title-second">Feature Select:</div>
    <div class="content-data">
      <a-select
        v-model="featureSelect"
        size="small"
        style="width: 100%"
        @change="handleChange_F"
      >
        <a-select-option value="decisionTree"> decisionTree </a-select-option>
      </a-select>
    </div>

    <a-divider dashed style="margin: 0.625rem 0" />

    <div class="title-second">Test Size:</div>
    <div class="content-data">
      <div class="strict-peptide-number">
        <a-input placeholder="Test Size Number" v-model="testSize" />
      </div>
    </div>

    <a-divider dashed style="margin: 0.625rem 0" />

    <div class="title-second">Random State:</div>
    <div class="content-data">
      <div class="strict-peptide-number">
        <a-input placeholder="Random State" v-model="randomState" />
      </div>
    </div>

    <a-divider dashed style="margin: 0.625rem 0" />

    <div class="title-second">Criterion:</div>
    <div class="content-data">
      <a-select
        v-model="criterion"
        size="small"
        style="width: 100%"
        @change="handleChange_C"
      >
        <a-select-option value="gini"> gini </a-select-option>
        <a-select-option value="entropy"> entropy </a-select-option>
      </a-select>
    </div>

    <a-divider dashed style="margin: 0.625rem 0" />

    <div class="title-second">Number of Tree:</div>
    <div class="content-data">
      <div class="strict-peptide-number">
        <a-input placeholder="Test Size Number" v-model="numOfTrees" />
      </div>
    </div>

    <a-divider dashed style="margin: 0.625rem 0" />

    <div class="title-second">Model Validation:</div>
    <div class="content-data">
      <a-select
        v-model="validation"
        size="small"
        style="width: 100%"
        @change="handleChange_M"
      >
        <a-select-option value="ROC"> ROC </a-select-option>
      </a-select>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";

export default {
  name: "SVM",

  props: {
    nodeId: {
      type: Number
    }
  },

  data() {
    return {
      params: {},
      featureSelect: "",
      testSize: "",
      randomState: "",
      criterion: "",
      numOfTrees: "",
      validation: ""
    };
  },

  computed: {
    ...mapState({
      projectId: state => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: state => state.workflowNode.workflowNodeInfo // 暂存每个已有的节点信息
    })
  },

  components: {
    changeName
  },

  mounted() {
    this.recoverData(this.nodeId);
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    this.params.featureSelect= this.featureSelect;
    this.params.testSize= this.testSize;
    this.params.randomState= this.randomState;
    this.params.criterion= this.criterion;
    this.params.numOfTrees= this.numOfTrees;
    this.params.validation= this.validation;
   
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach(element => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },

  methods: {
    handleChange_F (e) {
      this.featureSelect = e;
    },

    handleChange_C (e) {
      this.featureSelect = e;
    },

    handleChange_M (e) {
      this.validation = e;
    },

    // 复用函数，复原数据
    recoverData(a) {
      var that = this;
      that.workflowNodeInfo.forEach(element => {
        if (element.id == a && JSON.stringify(element.params) !== "{}") {
          this.featureSelect = element.params.featureSelect;
          this.testSize = element.params.testSize;
          this.randomState = element.params.randomState;
          this.criterion = element.params.criterion;
          this.numOfTrees = element.params.numOfTrees;
          this.validation = element.params.validation;
        }
        if (element.id == a && JSON.stringify(element.params) == "{}") {
          this.featureSelect = "";
          this.testSize = "";
          this.randomState = "";
          this.criterion = "";
          this.numOfTrees = "";
          this.validation = "";
        }
      });
    }
  }
};
</script>

