<template>
  <div class="Transform">
    <div class="title">Box Plot</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin: 0.625rem 0" />
    <div class="title-second">Instance Transform:</div>

    <div class="content-data">
      <div
        class="condition-tag"
        :style="{ background: '#5F7EC6', color: '#fff' }"
      >
        Filter condition:
      </div>
      <div
        class="anticon-tag-condition"
        v-for="(item, key) in Instance"
        :key="key"
      >
        <div
          class="condition-tag"
          :style="{ background: '#5F7EC6', color: '#fff' }"
        >
          <a @click="removedAllTag(item)">
            <i class="anticon anticon-close">
              <svg
                viewBox="64 64 896 896"
                data-icon="close"
                width="1em"
                height="1em"
                fill="currentColor"
                aria-hidden="true"
                class
              >
                <path
                  d="M563.8 512l262.5-312.9c4.4-5.2.7-13.1-6.1-13.1h-79.8c-4.7 0-9.2 2.1-12.3 5.7L511.6 449.8 295.1 191.7c-3-3.6-7.5-5.7-12.3-5.7H203c-6.8 0-10.5 7.9-6.1 13.1L459.4 512 196.9 824.9A7.95 7.95 0 0 0 203 838h79.8c4.7 0 9.2-2.1 12.3-5.7l216.5-258.1 216.5 258.1c3 3.6 7.5 5.7 12.3 5.7h79.8c6.8 0 10.5-7.9 6.1-13.1L563.8 512z"
                />
              </svg>
            </i>
          </a>
          {{ item.dimension }} / {{ item.function }}
        </div>
        <div style="text-align: center">
          <img
            src="../../../../../../assets/png/down.png"
            style="height: 20px; width: 20px"
          />
        </div>
      </div>
      <a-cascader
        size="small"
        :options="options"
        @change="onChange"
        style="margin-top: 0.625rem"
      ></a-cascader>
    </div>

    <a-divider dashed style="margin: 0.625rem 0" />
    <div class="title-second">Feature Transform:</div>

    <div class="content-data">
      <a-select
        mode="tags"
        size="small"
        style="width: 200px"
        placeholder="Feature Transform"
        :default-value="defaultValue"
        @change="handleChange"
        option-label-prop="label"
      >
        <a-select-opt-group>
          <span slot="label">Row</span>
          <a-select-option value="row/log2" label="row/log2">
            Log2
          </a-select-option>
          <a-select-option value="row/log10" label="row/log10">
            Log10
          </a-select-option>
          <a-select-option value="row/loge" label="row/loge">
            Loge
          </a-select-option>
          <a-select-option value="row/standardScale" label="row/standardScale">
            StandardScale
          </a-select-option>
          <a-select-option
            value="row/minMaxNormalize"
            label="row/minMaxNormalize"
          >
            MinMaxNormalize
          </a-select-option>
          <a-select-option value="row/normalizeL1" label="row/normalizeL1">
            NormalizeL1
          </a-select-option>
          <a-select-option value="row/normalizeL2" label="row/normalizeL2">
            NormalizeL2
          </a-select-option>
        </a-select-opt-group>
        <a-select-opt-group>
          <span slot="label">Column</span>
          <a-select-option value="column/log2" label="column/log2">
            Log2
          </a-select-option>
          <a-select-option value="column/log10" label="column/log10">
            Log10
          </a-select-option>
          <a-select-option value="column/loge" label="column/loge">
            Loge
          </a-select-option>
          <a-select-option
            value="column/standardScale"
            label="column/standardScale"
          >
            StandardScale
          </a-select-option>
          <a-select-option
            value="column/minMaxNormalize"
            label="column/minMaxNormalize"
          >
            MinMaxNormalize
          </a-select-option>
          <a-select-option
            value="column/normalizeL1"
            label="column/normalizeL1"
          >
            NormalizeL1
          </a-select-option>
          <a-select-option
            value="column/normalizeL2"
            label="column/normalizeL2"
          >
            NormalizeL2
          </a-select-option>
        </a-select-opt-group>
      </a-select>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";

export default {
  name: "Transform",

  props: {
    nodeId: {
      type: Number,
    },
  },

  data() {
    return {
      value: 1,
      params: {},
      // 自定义节点名称
      nodeName: "",
      // 是否开启修改
      inputNameShow: false,
      // 选择的数据收集
      Instance: [],
      // 维度筛选数据集
      options: [
        {
          value: "row",
          label: "row",
          children: [
            {
              value: "quantile normalize",
              label: "quantile normalize",
            },
            {
              value: "Fraction of Total",
              label: "Fraction of Total",
            },
          ],
        },
        {
          value: "column",
          label: "column",
          children: [
            {
              value: "quantile normalize",
              label: "quantile normalize",
            },
            {
              value: "Fraction of Total",
              label: "Fraction of Total",
            },
          ],
        },
      ],
      // 初始化数据
      defaultValue: [],
      dimension_r: {
        dimension: "row",
        featureTransform: [],
      },
      dimension_c: {
        dimension: "column",
        featureTransform: [],
      },
      featureTransform: [
        "log2",
        "log10",
        "loge",
        "standardScale",
        "minMaxNormalize",
        "normalizeL1",
        "normalizeL2",
      ],
    };
  },

  computed: {
    ...mapState({
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },

  components: {
    changeName,
  },

  mounted() {
    this.recoverData(this.nodeId);
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    this.params.featureTransform = [this.dimension_c, this.dimension_r];
    this.params.instanceTransform = this.Instance;
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach((element) => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },

  methods: {
    onChange(value) {
      this.Instance.push({
        dimension: value[0],
        function: value[1],
      });
    },

    // removedAllTag 移除当列所有筛选条件，n 为当前列的name
    removedAllTag(n) {
      // 删除计算机读取条件
      let index = this.Instance.indexOf(n);
      this.Instance.splice(index, 1);
    },

    handleChange(value) {
      this.defaultValue = value;
      value.forEach((res) => {
        if (res.substring(0, 3) == "row") {
          if (
            this.dimension_r.featureTransform.indexOf(res.substring(4)) == -1
          ) {
            this.dimension_r.featureTransform.push(res.substring(4));
          }
        } else {
          if (
            this.dimension_c.featureTransform.indexOf(res.substring(7)) == -1
          ) {
            this.dimension_c.featureTransform.push(res.substring(7));
          }
        }
      });
    },

    // 复用函数，复原数据
    recoverData(a) {
      this.workflowNodeInfo.forEach((element) => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {
            element.params.featureTransform.forEach(res =>{
              if(res.dimension == "row"){
                res.featureTransform.forEach(value =>{
                  this.defaultValue.push("row" + "/" + value);
                  this.dimension_r.featureTransform.push(value) ;
                });
              }else{
                res.featureTransform.forEach(value =>{
                  this.defaultValue.push("column" + "/" + value);
                this.dimension_c.featureTransform.push(value);
                })
                
              }
            })
            this.Instance = element.params.instanceTransform;
          }
        }
      });
    },
  },
};
</script>

