<template>
  <div class="MAF">
    <div class="title">survival analysis</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />
    <p class="tip">Clinical Data:</p>
    <div class="action">
      <span
        type="text"
        class="file-input-trigger"
        @click="libraryTable"
      >upload</span>
      <div class="content">
        <div
          class="file"
          v-for="(file, index) in params.libraryFiles"
          :key="index"
        >
          <div class="name">{{ file.name }}</div>
          <a-icon
            class="choice"
            type="close-circle"
            @click="removeFile(2, index)"
          />
        </div>
      </div>
    </div>
    <br>
    <div class="action">
      <span
        type="text"
        class="file-input-trigger"
        @click="getShow"
      >get attribute</span>
      <br>
      <br>
      <div v-show="show">
        <a-radio-group v-model="params.attribute">
          <a-radio value="Status column">
            <a-select
              :v-model="params.attribute==='Status column'?params.attributeValue:''"
              placeholder="Status column"
              style="width:10rem"
              @change="handleChange"
            >
              <a-select-option
                v-for="(i,index) in select"
                :key="index"
                :value="i"
              >
                {{i}}
              </a-select-option>
            </a-select>
          </a-radio>
          <a-radio value="Time">
            <a-select
              placeholder="Time"
              :v-model="params.attribute==='Time'?params.attributeValue:''"
              style="width:10rem;margin-top: 0.5rem;"
              @change="handleChange"
            >
              <a-select-option
                v-for="(i,index) in select"
                :key="index"
                :value="i"
              >
                {{i}}
              </a-select-option>
            </a-select>
          </a-radio>
          <a-radio value="Cox Regression">
            Cox Regression
          </a-radio>
        </a-radio-group>
      </div>
    </div>
    <LibraryTable
      :name="name"
      @survivalselections="survivalselections"
    />
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";
import LibraryTable from "./components/librarytable.vue";
const select = [
  'Case ID',
  'Proteome ID',
  'Genome ID',
  'Phosphoproteome ID',
  'RNA-seq ID',
  'Pathology reviewed',
  'Whole exome sequencing',
  'RNA-Seq',
  'Proteome',
  'phosphoproteome',
  'Gender',
  'Race',
  'Country',
  'History of neoadjuvant treatment',
  'Noninvasive bladder cancer therapy',
  'Tobacco smoking history',
  'Age at diagnosis',
  'TNM Stage',
  'AJCC Tumor category',
  'Vascular invasion',
  'Nerve invasion',
  'Presence of prostate cancer',
  'Presence of other cancer',
  'Vital status',
  'Months to last followup',
  'Months until death',
  'Combined days to last followup or death',
  'Pathology classification',
  'Histologic subtype',
  'Histologic grade',
  'NOS pathology',
  'Glandular differentiation',
  'Micropapillary differentiation',
  'Squamous differentiation',
  'Small cell differentiation',
  'Loss of adhesion',
  'Lymphoepithelimo differentiation',
  'Clear cell differentiation',
  'mRNA cluster',
  'proteome cluster',
  'phosphoproteome cluster',
  'Immune cluster'
]
export default {
  name: " MatrixMetabolome",
  props: {
    nodeId: {
      type: Number,
    },
  },
  data() {
    return {
      select,
      params: {
        data: {},
        libraryFiles: [],
        attribute: '',
        attributeValue: ''
      },
      name: "", //将自己的name给table组件
      show: false
    };
  },
  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    // this.params.MAFNumber = this.messageTags;
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach((element) => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },
  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },
  components: {
    changeName,
    LibraryTable,
  },
  mounted() {
    this.recoverData(this.nodeId);
  },
  methods: {
    handleChange(value) {
      this.params.attributeValue = value
    },
    getShow() {
      this.show = true
    },
    updateFile() {
      this.$refs.files.click();
    },
    libraryTable() {
      this.name = "survival";
      this.$store.commit("changelibraryTable", true);
    },
    handleFilesUpload() {
      let uploadedFiles = this.$refs.files.files;
      for (let i = 0; i < uploadedFiles.length; i++) {
        this.params.localFiles.push(uploadedFiles[i]);
      }
    },
    removeFile(index, file) {
      if (index == 1) {
        this.params.localFiles.splice(file, 1);
      } else {
        this.params.libraryFiles.splice(file, 1);
      }
    },
    survivalselections(e, libraryid) {
      this.params.libraryFiles = e;
      this.params.libraryFiles.forEach((item) => {
        item["libraryid"] = libraryid;
      });
    },
    //复用函数
    recoverData(a) {
      this.workflowNodeInfo.forEach((element) => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {

            this.params.libraryFiles = element.params.libraryFiles;
            this.params.attribute = element.params.attribute;
          }
        }
      });
    },
  },
};
</script>

