        >
          <template v-for="(tag, index) in messageTags_1">
            <a-tag
              :key="tag"
              :closable="index == index"
              :afterClose="() => handleClose_1(tag)"
              >{{ tag }}</a-tag
            >
          </template>
          <div class="add-tag">
