        placeholder="Dimension"
      ></a-cascader>
      <div class="range-Number">
        <input type="text" v-model="rangeMin" placeholder="Range" />
        ~
        <input type="text" v-model="rangeMax" placeholder="Range" />
        <button @click="submitRange">⬆</button>
      </div>
    </div>
  </div>
