<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";

export default {
  name: "FilterEXP",

  props: {
    nodeId: {
      type: Number,
    },
  },

  data() {
    return {
      params: {},
      // 自定义节点名称
      nodeName: "",
      // 是否开启修改
      inputNameShow: false,
      messageTagData_0: "",
      messageTags_0: [],
      messageTagData_1: "",
      messageTags_1: [],
      // 选择的数据收集
      aggregation_0: [],
      aggregation_1: [],
      // 维度筛选数据集
      options: [
        {
          value: "row",
          label: "row",
          children: [
            {
              value: "mean",
              label: "mean",
            },
            {
              value: "median",
              label: "median",
            },
            {
              value: "sum",
              label: "sum",
            },
            {
              value: "max",
              label: "max",
            },
            {
              value: "min",
              label: "min",
            },
          ],
        },
        {
          value: "column",
          label: "column",
          children: [
            {
              value: "mean",
              label: "mean",
            },
            {
              value: "median",
              label: "median",
            },
            {
              value: "sum",
              label: "sum",
            },
            {
              value: "max",
              label: "max",
            },
            {
              value: "min",
              label: "min",
            },
          ],
        },
      ],
      // rank维度条件
      rankDimension: {},
      // 记录rank的最小值
      rankMin: "",
      // 记录rank的最大值
      rankMax: "",
      // range维度条件
      rangeDimension: {},
      // 记录range的最小值
      rangeMin: "",
      // 记录range的最大值
      rangeMax: "",
    };
  },

  computed: {
    ...mapState({
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },

  components: {
    changeName,
  },

  mounted() {
    this.recoverData(this.nodeId);
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    this.params.rownames = this.messageTags_0;
    this.params.columnnames = this.messageTags_1;
    this.params.rank = this.aggregation_0;
    this.params.range = this.aggregation_1;
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach((element) => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },

  methods: {
    // 处理自定义写入实验数据
    handleMessageData_0() {
      let arr = this.messageTagData_0.split(","); //字符分割
      arr.forEach((element) => {
        if (element === "" || this.messageTags_0.indexOf(element) > -1) {
          return;
        }
        this.messageTags_0.push(element);
      });
      // 输入完成之后清除输入框
      this.messageTagData_0 = "";
    },

    handleClose_0(removedTag) {
      this.messageTags_0.splice(this.messageTags_0.indexOf(removedTag), 1);
    },

    // 使用delete按键删除事件
    handleMessageDelete_0() {
      if (this.messageTagData_0 === "") {
        // 输入框中没有任何数据的时候，删除数组中的最后一个元素。改变原数组！
        this.messageTags_0.pop();
      }
    },

    // 点击聚焦事件
    getFocus_0() {
      this.$refs.message_0.focus();
    },

    // 处理自定义写入实验数据
    handleMessageData_1() {
      let arr = this.messageTagData_1.split(","); //字符分割
      arr.forEach((element) => {
        if (element === "" || this.messageTags_1.indexOf(element) > -1) {
          return;
        }
        this.messageTags_1.push(element);
      });
      // 输入完成之后清除输入框
      this.messageTagData_1 = "";
    },

    handleClose_1(removedTag) {
      this.messageTags_1.splice(this.messageTags_1.indexOf(removedTag), 1);
    },

    // 使用delete按键删除事件
    handleMessageDelete_1() {
      if (this.messageTagData_1 === "") {
        // 输入框中没有任何数据的时候，删除数组中的最后一个元素。改变原数组！
        this.messageTags_1.pop();
      }
    },

    // 点击聚焦事件
    getFocus_1() {
      this.$refs.message_1.focus();
    },

    onChange_rank(value) {
      this.rankDimension = {
        dimension: value[0],
        function: value[1],
      };
    },

    submitRank() {
      if (
        typeof Number(this.rankMax) != Number &&
        typeof Number(this.rankMin) != Number &&
        Object.keys(this.rankDimension).length == 0
      ) {
        return;
      } else {
        this.aggregation_0.push({
          dimension: this.rankDimension.dimension,
          function: this.rankDimension.function,
          descendingRank: [this.rankMin, this.rankMax],
        });
      }
    },

    onChange_range(value) {
      this.rangeDimension = {
        dimension: value[0],
        function: value[1],
      };
    },

    submitRange() {
      if (
        typeof Number(this.rangeMax) != Number &&
        typeof Number(this.rangeMin) != Number &&
        Object.keys(this.rangeDimension).length == 0
      ) {
        return;
      } else {
        this.aggregation_1.push({
          dimension: this.rangeDimension.dimension,
          function: this.rangeDimension.function,
          range: [this.rangeMin, this.rangeMax],
        });
      }
    },

    // removedAllTag 移除当列所有筛选条件，n 为当前列的name
    removedAllTag_0(n) {
      // 删除计算机读取条件
      let index = this.aggregation_0.indexOf(n);
      this.aggregation_0.splice(index, 1);
    },

    // removedAllTag 移除当列所有筛选条件，n 为当前列的name
    removedAllTag_1(n) {
      // 删除计算机读取条件
      let index = this.aggregation_1.indexOf(n);
      this.aggregation_1.splice(index, 1);
    },

    // 复用函数，复原数据
    recoverData(a) {
      this.workflowNodeInfo.forEach((element) => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {
            this.messageTags_0 = element.params.rownames;
            this.messageTags_1 = element.params.columnnames;
            this.aggregation_0 = element.params.rank;
            this.aggregation_1 = element.params.range;
          }
        }
      });
    },
  },
};
</script>

