<template>
  <div class="DifferentialExpressedGenes">
    <div class="title">Differential Expressed Genes</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />
    <!-- 选择两个label -->
    <div class="choose-label">
      <div class="title-second">Choose Two Labels:</div>
      <div class="content-data">
        <div class="title-third">GroupA:</div>
        <a-select
          style="width: 100%"
          @change="handleChangeOne"
          size="small"
          v-model="groupNameA"
        >
          <a-select-option
            v-for="item in labels"
            :key="item.idx"
            :value="item.labelName"
          >{{ item.labelName }}</a-select-option>
        </a-select>
        <div class="title-third">GroupB:</div>
        <a-select
          style="width: 100%"
          @change="handleChangeTwo"
          size="small"
          v-model="groupNameB"
        >
          <a-select-option
            v-for="item in labels"
            :key="item.idx"
            :value="item.labelName"
          >{{ item.labelName }}</a-select-option>
        </a-select>
      </div>
    </div>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />
    <div class="title-second">Fold change:</div>
    <div class="content-data">
      <div class="title-third">Ratio A/B:</div>
      <a-input
        placeholder="Fold Change"
        v-model="ratioAB"
        ref="ratioAB"
        size="small"
      >
        <icon-font
          slot="prefix"
          type="icon-fenshu"
        />
        <a-icon
          v-if="ratioAB"
          slot="suffix"
          type="close-circle"
          @click="emitEmpty1"
        />
      </a-input>
      <div class="title-third">Ratio B/A:</div>
      <a-input
        placeholder="Fold Change"
        v-model="ratioBA"
        ref="ratioBA"
        size="small"
      >
        <icon-font
          slot="prefix"
          type="icon-fenshu"
        />
        <a-icon
          v-if="ratioBA"
          slot="suffix"
          type="close-circle"
          @click="emitEmpty2"
        />
      </a-input>
    </div>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />
    <div class="title-second">P &nbsp;value:</div>
    <div class="content-data">
      <a-input
        placeholder="pValue"
        v-model="pValue"
        ref="pValue"
        size="small"
      >
        <icon-font
          slot="prefix"
          type="icon-zhixuanzhong"
        />
        <a-icon
          v-if="pValue"
          slot="suffix"
          type="close-circle"
          @click="emitEmpty_P"
        />
      </a-input>
    </div>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />
    <div class="title-second">Significance test:</div>
    <div class="content-data">
      <a-radio-group
        @change="onChange"
        v-model="value"
      >
        <a-radio :value="1">Student's T test</a-radio>
        <a-radio :value="2">Wilcox test</a-radio>
      </a-radio-group>
    </div>
    <div
      v-if="value===1"
      class="title-second"
    >equal_var:</div>
    <div
      class="content-data"
      v-if="value===1"
    >
      <a-radio-group v-model="params.equal_var">
        <a-radio :value="true">TRUE</a-radio>
        <a-radio :value="false">FALSE</a-radio>
      </a-radio-group>
    </div>
    <div
      v-if="value===2"
      class="title-second"
    >alternative:</div>
    <div
      class="content-data"
      v-if="value===2"
    >
      <a-radio-group v-model="params.alternative">
        <a-radio value="two-sided">two-sided</a-radio>
        <a-radio value="greater">greater</a-radio>
        <a-radio value="less">less</a-radio>
      </a-radio-group>
    </div>
    <div class="title-second">p adjusted methods:</div>
    <div class="content-data">
      <a-select
        style="width: 100%"
        @change="handleChangenewVaule"
        size="small"
        v-model="Padjuested"
      >
        <a-select-option
          v-for="item in selectvalue"
          :key="item"
          :value="item"
        >{{ item }}</a-select-option>
      </a-select>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { Icon } from "ant-design-vue";
import changeName from "./components/changeName";

const IconFont = Icon.createFromIconfontCN({
  scriptUrl: "//at.alicdn.com/t/font_1910387_oco2ep4c59.js",
});
const selectvalue = [
  'holm',
  'hochberg',
  'hommel',
  'bonferroni',
  'BH',
  'BY',
  'fdr',
  'none'

]
export default {
  name: "DifferentialExpressedGenes",

  props: {
    nodeId: {
      type: Number,
    },
  },

  data() {
    return {
      // 用来储存请求的label数据
      labels: [],
      selectvalue,
      // 储存选择的比较的两个label
      groupNameA: "A",
      groupNameB: "B",
      ratioAB: "",
      ratioBA: "",
      pValue: "",
      Padjuested: 'holm',
      equal_var: true,
      alternative: '',
      // Significance test的单选框的值
      value: 1,
      params: {
        groupA: "",
        groupB: "",
        ratioAB: 0,
        ratioBA: 0,
        testFunction: "ttest",
        pValue: 0,
        Padjuested: '',
        equal_var: true,
        alternative: ''
      },
    };
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },

  components: {
    IconFont,
    changeName,
  },

  mounted() {
    // 数据复原函数
    this.recoverData(this.nodeId);
    // 请求label函数
    this.getLabelData();
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    if (this.value == 1) {
      this.params.testFunction = "ttest";
    } else {
      this.params.testFunction = "wilcox-test";
    }
    this.params.groupA = this.groupNameA;
    this.params.groupB = this.groupNameB;
    this.params.Padjuested = this.Padjuested
    this.params.ratioAB = Number(this.ratioAB);
    this.params.ratioBA = Number(this.ratioBA);
    this.params.pValue = Number(this.pValue);
    let obj = Object.assign({}, this.params);
    let arr = Array.from(this.workflowNodeInfo);
    arr.forEach((element) => {
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },

  methods: {
    //输入框清空
    emitEmpty1() {
      this.$refs.ratioAB.focus();
      this.ratioAB = "";
    },
    emitEmpty2() {
      this.$refs.ratioBA.focus();
      this.ratioBA = "";
    },

    //单选框
    onChange(e) {
      if (e.target.value == 1) {
        this.params.testFunction = "ttest";
      } else {
        this.params.testFunction = "wilcox-test";
      }
    },

    //输入框清空
    emitEmpty_P() {
      this.$refs.pValue.focus();
      this.pValue = "";
    },

    //choose label
    handleChangeOne(value) {
      this.groupNameA = value;
    },
    handleChangeTwo(value) {
      this.groupNameB = value;
    },
    handleChangenewVaule(value) {
      this.Padjuested = value
    },

    // 复用函数，请求label
    getLabelData() {
      let id = this.projectId;
      //labelInfo
      this.$ProjectModels
        .getAllLabelModels(id)
        .then((res) => {
          this.labels = res.data.labels;
        })
        .catch((error) => window.console.error("Error:", error));
    },

    // 复用函数逻辑，自动填充
    recoverData(a) {
      this.workflowNodeInfo.forEach((element) => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {
            this.groupNameA = element.params.groupA;
            this.groupNameB = element.params.groupB;
            this.ratioAB = element.params.ratioAB;
            this.ratioBA = element.params.ratioBA;
            this.Padjuested = element.params.Padjuested;
            if (element.params.testFunction == "ttest") {
              this.value = 1;
            } else {
              this.value = 2;
            }
            this.pValue = element.params.pValue;
          }
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.DifferentialExpressedGenes {
  padding: 0rem 1.25rem 1.875rem 1.25rem;
  .title {
    font-size: 1.125rem;
    font-style: italic;
    font-weight: 600;
  }
  .title-second {
    margin: .9375rem 0 .9375rem 0;
    font-weight: 550;
  }
  .title-third {
    margin: .9375rem 0 .3125rem 0;
    font-size: .75rem;
  }
  .content-data {
    padding: 0rem 0rem .625rem .9375rem;
  }
}
</style>