<template>
  <div class="MAF">
    <div class="title">Multi-omics input matrix</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />
    <p class="tip">Proteome:</p>
    <a-input v-model="params.Proteome" />
    <p class="tip">PTM:</p>
    <a-input v-model="params.PTM" />
    <p class="tip">CNV:</p>
    <div class="action">
      <span
        type="text"
        class="file-input-trigger"
        @click="libraryTable('CNV')"
      >upload</span>
      <div class="content">
        <div
          class="file"
          v-for="(file, index) in params.CNV"
          :key="index"
        >
          <div class="name">{{ file.name }}</div>
          <a-icon
            class="choice"
            type="close-circle"
            @click="removeFile(1, index)"
          />
        </div>
      </div>
    </div>
    <p class="tip">SNV:</p>
    <div class="action">
      <span
        type="text"
        class="file-input-trigger"
        @click="libraryTable('SNV')"
      >upload</span>
      <div class="content">
        <div
          class="file"
          v-for="(file, index) in params.SNV"
          :key="index"
        >
          <div class="name">{{ file.name }}</div>
          <a-icon
            class="choice"
            type="close-circle"
            @click="removeFile(2, index)"
          />
        </div>
      </div>
    </div>
    <p class="tip">RNAseq:</p>
    <div class="action">
      <span
        type="text"
        class="file-input-trigger"
        @click="libraryTable('RNAseq')"
      >upload</span>
      <div class="content">
        <div
          class="file"
          v-for="(file, index) in params.RNAseq"
          :key="index"
        >
          <div class="name">{{ file.name }}</div>
          <a-icon
            class="choice"
            type="close-circle"
            @click="removeFile(3, index)"
          />
        </div>
      </div>
    </div>
    <p class="tip">Metabolome:</p>
    <div class="action">
      <span
        type="text"
        class="file-input-trigger"
        @click="libraryTable('Metabolome')"
      >upload</span>
      <div class="content">
        <div
          class="file"
          v-for="(file, index) in params.Metabolome"
          :key="index"
        >
          <div class="name">{{ file.name }}</div>
          <a-icon
            class="choice"
            type="close-circle"
            @click="removeFile(4, index)"
          />
        </div>
      </div>
    </div>
    <LibraryTable
      :name="name"
      @CNFselections="CNFselections"
      @SNVselections="SNVselections"
      @RNAseqselections="RNAseqselections"
      @Metabolomeselections="Metabolomeselections"
    />
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";
import LibraryTable from "./components/librarytable.vue";
export default {
  name: "matrix",
  props: {
    nodeId: {
      type: Number,
    },
  },
  data() {
    return {
      params: {
        data: {},
        Proteome: '',
        PTM: '',
        CNV: [],
        SNV: [],
        RNAseq: [],
        Metabolome: []
      },
      name: "", //将自己的name给table组件
    };
  },
  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    // this.params.MAFNumber = this.messageTags;
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach((element) => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },
  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },
  components: {
    changeName,
    LibraryTable,
  },
  mounted() {
    this.recoverData(this.nodeId);
  },
  methods: {
    updateFile() {
      this.$refs.files.click();
    },
    libraryTable(name) {
      this.name = name;
      this.$store.commit("changelibraryTable", true);
    },
    handleFilesUpload() {
      let uploadedFiles = this.$refs.files.files;
      for (let i = 0; i < uploadedFiles.length; i++) {
        this.params.localFiles.push(uploadedFiles[i]);
      }
    },
    removeFile(index, file) {
      if (index == 1) {
        this.params.CNV.splice(file, 1);
      } else if (index == 2) {
        this.params.SNV.splice(file, 1);
      } else if (index == 3) {
        this.params.RNAseq.splice(file, 1);
      } else if (index == 4) {
        this.params.Metabolome.splice(file, 1);
      }
    },
    CNFselections(e, libraryid) {
      this.params.CNV = e;
      this.params.CNV.forEach((item) => {
        item["libraryid"] = libraryid;
      });
    },
    SNVselections(e, libraryid) {
      this.params.SNV = e;
      this.params.SNV.forEach((item) => {
        item["libraryid"] = libraryid;
      });
    },
    RNAseqselections(e, libraryid) {
      this.params.RNAseq = e;
      this.params.RNAseq.forEach((item) => {
        item["libraryid"] = libraryid;
      });
    },
    Metabolomeselections(e, libraryid) {
      this.params.Metabolome = e;
      this.params.Metabolome.forEach((item) => {
        item["libraryid"] = libraryid;
      });
    },
    //复用函数
    recoverData(a) {
      this.workflowNodeInfo.forEach((element) => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {
            this.params.Proteome = element.params.Proteome;
            this.params.PTM = element.params.PTM;
            this.params.CNV = element.params.CNV;
            this.params.SNV = element.params.SNV;
            this.params.RNAseq = element.params.RNAseq;
            this.params.Metabolome = element.params.Metabolome;
          }
        }
      });
    },
  },
};
</script>

