<template>
  <div class="Oncoplot">
    <div class="title">Oncoplot</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />
    <div class="filter">
      <div class="title">please chioce two labels</div>
      <div class="title-second">
        <a-radio-group v-model="params.name">
          <a-radio value="top">
            top N
          </a-radio>
          <a-radio value="genes">
            seleceted genes
          </a-radio>
        </a-radio-group>
      </div>
      <br>
      <div
        class="title-second"
        v-if="params.name==='top'"
      >

        <div class="title">please input number：</div>
        <br>
        <a-input-number
          style="width:100%"
          id="inputNumber"
          v-model="params.value"
        />
      </div>
      <div
        class="title-second"
        v-if="params.name==='genes'"
      >
        please input genes
        <a-textarea
          placeholder="please input genes"
          v-model="params.value"
          auto-size
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";

export default {
  name: "Oncoplot",
  props: {
    nodeId: {
      type: Number,
    },
  },
  data() {
    return {
      params: {
        name: 'top',
        value: ''
      },
    };
  },
  mounted() {
    // 数据复原函数
    this.recoverData(this.nodeId);
    // 请求label函数
    this.getLabelData();
  },
  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },
  components: {
    changeName,
  },
  beforeDestroy() {
    let obj = Object.assign({}, this.params);
    let arr = Array.from(this.workflowNodeInfo);
    arr.forEach((element) => {
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },
  methods: {
    // 复用函数，请求label
    getLabelData() {
      let id = this.projectId;
      //labelInfo
      this.$ProjectModels
        .getAllLabelModels(id)
        .then((res) => {
          this.labels = res.data.labels;
        })
        .catch((error) => window.console.error("Error:", error));
    },
    // 复用函数逻辑，自动填充
    recoverData(a) {
      this.workflowNodeInfo.forEach((element) => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {
            this.params = element.params
          }
        }
      });
    },
  }
};
</script>

<style lang="scss" scoped>
.Oncoplot {
  padding: 0rem 1.25rem 1.875rem 1.25rem;
  .title {
    font-weight: 600;
  }
  .title-second {
    margin: .9375rem 0 .3125rem 0;
    font-size: .875rem;
  }
}
</style>