<template>
  <div class="PPI">
    <div class="title">Tumor Mutation Burden</div>

    <a-divider style="margin:0.625rem 0;" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider
      dashed
      style="margin:0.625rem 0;"
    />
    <div class="filter">
      <div class="title-second">please chioce logScale:</div>
      <div class="title-second">
        <a-radio-group v-model="params.logScale">
          <a-radio value="True">
            True
          </a-radio>
          <a-radio value="False">
            False
          </a-radio>
        </a-radio-group>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";

export default {
  name: "TMB",
  data() {
    return {
      params: {
        logScale: 'True'
      },
    }
  },

  mounted() {
    // 数据复原函数
    this.recoverData(this.nodeId);
    // 请求label函数
    this.getLabelData();
  },
  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },
  components: {
    changeName,
  },
  beforeDestroy() {
    let obj = Object.assign({}, this.params);
    let arr = Array.from(this.workflowNodeInfo);
    arr.forEach((element) => {
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },
  methods: {
    // 复用函数，请求label
    getLabelData() {
      let id = this.projectId;
      //labelInfo
      this.$ProjectModels
        .getAllLabelModels(id)
        .then((res) => {
          this.labels = res.data.labels;
        })
        .catch((error) => window.console.error("Error:", error));
    },
    // 复用函数逻辑，自动填充
    recoverData(a) {
      this.workflowNodeInfo.forEach((element) => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {
            this.params = element.params
          }
        }
      });
    },
  }
};
</script>

