<template>
  <div class="ChooseData">
    <div class="title">Generate Quantitive Matrix</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />
    <div class="title-second">Choose data:</div>
    <div class="choose-data content-data">
      <div class="title-third">Exp:</div>
      <div
        class="choose-exp"
        @click="chooseExp"
        style="overflow: hidden; text-overflow: ellipsis; white-space: nowrap"
        title="Choose all the experiments"
      >
        Choose all the experiments
      </div>
      <div class="title-third">Label:</div>
      <div class="choose-label">
        <a-select
          mode="tags"
          class="select-input"
          style="width: 100%"
          @change="chooseLabel"
          placeholder="Choose Label"
          size="small"
          v-model="labelName"
        >
          <a-select-option
            v-for="item in labels"
            :key="item.labelName.toString(36)"
          >{{ item.labelName }}</a-select-option>
        </a-select>
      </div>
      <div class="title-third">Group:</div>
      <div class="choose-group">
        <a-select
          defaultValue="Choose Group"
          style="width: 100%"
          @change="handleChange"
          size="small"
          v-model="groupName"
        >
          <a-select-option
            v-for="(item, id) in groupInfo"
            :key="id"
            :value="item.name"
          >{{ item.name }}</a-select-option>
        </a-select>
      </div>
    </div>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />
    <div class="title-second">iProteome Filter:</div>
    <div class="content-data">
      <div class="title-third">Unique Peptide Number:</div>
      <div class="unique-peptide-number">
        <a-input
          placeholder="Unique Peptide Number"
          v-model="uniqueNumber"
          ref="uniqueNumber"
          size="small"
        >
          <a-icon
            slot="prefix"
            type="right-square"
          />
          <a-icon
            v-if="uniqueNumber"
            slot="suffix"
            type="close-circle"
            @click="emitEmpty1"
          />
        </a-input>
      </div>
      <div class="title-third">Strict Peptide Number:</div>
      <div class="strict-peptide-number">
        <a-input
          placeholder="Strict Peptide Number"
          v-model="strictNumber"
          ref="strictNumber"
          size="small"
        >
          <a-icon
            slot="prefix"
            type="right-square"
          />
          <a-icon
            v-if="strictNumber"
            slot="suffix"
            type="close-circle"
            @click="emitEmpty2"
          />
        </a-input>
      </div>
    </div>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />
    <div class="zeroValue">
      <div class="title-second">Zero Value:</div>
      <div class="content-data">
        <a-radio-group
          @change="onChange_0"
          v-model="value_0"
        >
          <a-radio :value="1">Keep as Zero</a-radio>
          <a-radio :value="2">As Missing Value</a-radio>
          <a-radio :value="3">Remove</a-radio>
        </a-radio-group>
      </div>
    </div>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />
    <div class="missing-value">
      <div class="title-second">Missing Value:</div>
      <div class="content-data">
        <a-radio-group
          @change="onChange_1"
          v-model="value_1"
        >
          <a-radio :value="1">Keep as NA</a-radio>
          <a-radio :value="2">Min Value</a-radio>
        </a-radio-group>
      </div>
    </div>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />
    <div class="instance-transform">
      <div class="title-second">Instance Transform:</div>
      <div class="content-data">
        <a-radio-group
          @change="onChange_2"
          v-model="value_2"
        >
          <a-radio :value="1">Quantile Normalize</a-radio>
          <a-radio :value="2">Fraction of Total</a-radio>
        </a-radio-group>
      </div>
    </div>

    <a-divider
      dashed
      style="margin: 0.625rem 0"
    />
    <div class="feature-transform">
      <div class="title-second">Feature Transform:</div>
      <div class="choose-group content-data">
        <a-select
          mode="tags"
          class="select-input"
          style="width: 100%"
          @change="changeFeature"
          placeholder="Feature Transform"
          size="small"
          v-model="featureTransform"
        >
          <a-select-option
            v-for="item in featureTransforms"
            :key="item.toString(36)"
          >{{ item }}</a-select-option>
        </a-select>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import state from "../../../../../../store/state";
import changeName from "./components/changeName";
export default {
  name: "ChooseData",

  props: {
    nodeId: {
      type: Number,
    },
  },

  data() {
    return {
      // iProteome Filter 信息
      uniqueNumber: "",
      strictNumber: "",
      // 组件信息暂存对象
      params: {
        data: {},
        iProteomeFilter: {
          UPN: 0,
          SPN: 0,
        },
        zeroValue: "keep as zero",
        missingValue: "Keep as NA",
        instanceTransform: "Quantile Normalize",
      },
      // 单选框一号
      value_0: 1,
      // 单选框二号
      value_1: 1,
      // 单选框三号
      value_2: 1,
      // 服务端获取的组信息
      groupInfo: [],
      // 选中的组名称
      groupName: "",
      // 服务端获取的实验标签信息
      labels: [],
      // 抽离存放的实验标签信息名称
      labelNames: [],
      // 选中的实验标签信息名称
      labelName: [],

      // featureTransform备选数组
      featureTransforms: [
        "log2",
        "log10",
        "loge",
        "standardScale",
        "minMaxNormalize",
        "normalizeL1",
        "normalizeL2",
      ],
      // 选中的featureTransform信息
      featureTransform: [],
    };
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },

  components: {
    changeName,
  },

  mounted() {
    // 数据复现函数
    this.recoverData(this.nodeId);
    // 请求label函数
    this.getLabelData();
    // 请求组数据函数
    this.getGroupData();
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    // 判断是否存在
    if (this.labelName.length > 0) {
      // 记录选择得label
      this.params.data = {
        labels: this.labelName,
      };
    } else if (this.groupName !== "") {
      // 记录选择得group
      this.params.data = {
        groups: this.groupName,
      };
    } else {
      // 记录其他数据
      this.params.data = this.params.data;
    }
    this.params.iProteomeFilter.UPN = Number(this.uniqueNumber);
    this.params.iProteomeFilter.SPN = Number(this.strictNumber);
    this.params.featureTransform = this.featureTransform;
    // 重新记录当前单选框记录
    if (this.value_0 === 1) {
      this.params.zeroValue = "keep as zero";
    } else if (this.value_0 === 2) {
      this.params.zeroValue = "as missing value";
    } else {
      this.params.zeroValue = "remove";
    }
    if (this.value_1 == 1) {
      this.params.missingValue = "Keep as NA";
    } else {
      this.params.missingValue = "Min Value";
    }
    if (this.value_2 == 1) {
      this.params.instanceTransform = "Quantile Normalize";
    } else {
      this.params.instanceTransform = "Fraction of Total";
    }
    let obj = Object.assign({}, this.params);
    let arr = Array.from(this.workflowNodeInfo);
    arr.forEach((element) => {
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },

  methods: {
    chooseExp() {
      let value = {};
      value.All = "True";
      this.params.data = value;
      this.$store.commit("getLabelName", this.labelNames);
    },

    chooseLabel(value) {
      console.log(value);
      // this.$emit("labelValue", value);
      this.GQMtoVannPlot = value;
      this.$store.commit("getLabelName", value);
    },

    handleChange(value) {
      window.console.log(`selected ${value}`);
    },

    changeFeature(value) {
      window.console.log(`selected ${value}`);
    },

    //输入框清空
    emitEmpty1() {
      this.$refs.uniqueNumber.focus();
      this.uniqueNumber = "";
    },
    emitEmpty2() {
      this.$refs.strictNumber.focus();
      this.strictNumber = "";
    },

    //单选框
    onChange_0(e) {
      if (e.target.value == 1) {
        this.params.zeroValue = "keep as zero";
      } else if (this.value === 2) {
        this.params.zeroValue = "as missing value";
      } else {
        this.params.zeroValue = "remove";
      }
    },

    onChange_1(i) {
      if (i.target.value == 1) {
        this.params.missingValue = "Keep as NA";
      } else {
        this.params.missingValue = "Min Value";
      }
    },

    onChange_2(i) {
      if (i.target.value == 1) {
        this.params.instanceTransform = "Quantile Normalize";
      } else {
        this.params.instanceTransform = "Fraction of Total";
      }
    },

    // 复用函数，复原数据
    recoverData(a) {
      this.workflowNodeInfo.forEach((element) => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {
            if (element.params.iProteomeFilter.UPN) {
              this.uniqueNumber = element.params.iProteomeFilter.UPN;
            } else {
              this.uniqueNumber = "";
            }
            if (element.params.iProteomeFilter.SPN) {
              this.strictNumber = element.params.iProteomeFilter.UPN;
            } else {
              this.strictNumber = "";
            }
            if (!element.params.data.labels) {
              this.labelName = [];
            } else {
              this.labelName = element.params.data.labels;
            }
            if (element.params.zeroValue == "keep as zero") {
              this.value_0 = 1;
            } else if (element.params.zeroValue == "as missing value") {
              this.value_0 = 2;
            } else {
              this.value_0 = 3;
            }
            if (element.params.missingValue == "Keep as NA") {
              this.value_1 = 1;
            } else {
              this.value_1 = 2;
            }
            if (element.params.instanceTransform == "Quantile Normalize") {
              this.value_2 = 1;
            } else {
              this.value_2 = 2;
            }
            this.featureTransform = element.params.featureTransform;
          }
        }
      });
    },

    // 复用函数，请求label
    getLabelData() {
      let id = this.projectId;
      //labelInfo
      this.$ProjectModels
        .getAllLabelModels(id)
        .then((res) => {
          this.labels = res.data.labels;
          for (let i in res.data.labels) {
            this.labelNames.push(res.data.labels[i].labelName);
          }
        })
        .catch((error) => window.console.error("Error:", error));
    },

    // 请求group数据
    getGroupData() {
      let id = this.projectId;
      // groupInfo
      this.$ProjectModels
        .getGroupsInfoModels(id)
        .then((ind) => {
          this.groupInfo = ind.data.groups;
        })
        .catch((error) => window.console.error("Error:", error));
    },
  },

  watch: {
    nodeId(a) {
      this.recoverData(a);
    },
    projectId() {
      this.getLabelData();
      this.getGroupData();
    },
  },
};
</script>

