<template>
  <div class="Kinase">
    <div class="title">Kinase activity analysis</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin: 0.625rem 0" />
    <div class="filter">
      <div class="title-second">species:</div>
      <a-radio-group
        v-model="params.value"
        :options="options"
        @change="onChange"
      />
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";
const options = [
  { label: "Human ", value: "Human " },
  { label: "Mouse ", value: "Mouse " },
];
export default {
  name: "Kinase", //柱状图
  props: {
    nodeId: {
      type: Number,
    },
  },
  data() {
    return {
      options,
      params: {
        data: {},
        value: "Human",
      },
    };
  },
  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    // this.params.MAFNumber = this.messageTags;
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach((element) => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },
  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },
  components: {
    changeName,
  },
  methods: {
    onChange(e) {
      this.params.value = e.target.value;
    },
  },
};
</script>

<style lang="scss" scoped>
.Kinase {
  padding: 0rem 1.25rem 1.875rem 1.25rem;
  .title {
    font-weight: 600;
  }
  .title-second {
    font-size: 0.875rem;
    font-weight: 600;
    margin: 0.9375rem 0 0.3125rem 0;
  }
}
</style>