<!--
* slider作为元素创建区域，在此区域创建一个可以拖拽的半透明元素
* container作为元素放置区域，当鼠标到达此区域时，显示为可放置状态，当松开鼠标的时候，在此区域相同坐标位置立即创建一个相同的元素
 -->

<template>
  <div class="canvas-workflow" id="canvas">
    <i class="iconfont-none" id="icon_0">&#xe62d;</i>
    <i class="iconfont-none" id="icon_1">&#xe7d1;</i>
    <i class="iconfont-none" id="icon_2">&#xe7d2;</i>
    <div class="title">
      <div class="title-left">
        <slot name="canvasTools"></slot>
      </div>
      <div class="title-right">
        <slot name="canvasClear"></slot>
        <div class="circular" style="float: right" @click="decreaseScale">
          <i class="iconfont">&#xe652;</i>
        </div>
        <div class="circular" style="float: right" @click="increaseScale">
          <i class="iconfont">&#xe651;</i>
        </div>
      </div>
    </div>
    <canvas
      :class="[classStyleL, classStyleP]"
      id="main"
      :height="canvasHeight"
      :width="canvasWidth"
      @click="canvasClick($event)"
      @mousedown="onMousedown($event)"
      @mouseout="onMouseout"
      @mouseup="onMouseup($event)"
      @mousemove="onMousemove($event)"
      :style="{
        transform: 'translate(' + this.canvasX + 'px ,' + this.canvasY + 'px)',
      }"
    ></canvas>
  </div>
</template>

<script>
import nodeRD from "./canvasWorkflow/nodes/nodeRoundeDrectangle";
import canvasMove from "./canvasWorkflow/canvas/canvasMove";
import canvasScale from "./canvasWorkflow/canvas/canvasScale";
import drawLine from "./canvasWorkflow/line/drawLine";

export default {
  name: "canvasWorkflow",

  /*
  混入methods：
    1. fillRoundRect() 绘制node的主要函数  drawRoundRectPath() 绘制node矩形的圆角的函数
    // 待续
    */
  mixins: [nodeRD, canvasMove, canvasScale, drawLine],

  props: {
    nodeDatas: {
      type: [Object, Array],
    },
    pathDatas: {
      type: [Object, Array],
    },
  },

  data() {
    return {
      // 画布坐标信息，用于画布的整体拖拽
      canvasX: null,
      // 画布坐标信息，用于画布的整体拖拽
      canvasY: null,
      // 画布节点坐标信息，用于画布的整体拖拽
      canvasNodeX: null,
      // 画布节点坐标信息，用于画布的整体拖拽
      canvasNodeY: null,
      // 画布大小，用于根据分辨率来缩放画布
      canvasHeight: 2500,
      canvasWidth: 3000,
      // 节点坐标数据，基本信息
      nodesListData: [],
      // 缩放倍数
      zoom: 1,
      // 字体大小
      fontSize: 16,
      // 画布是否可以移动
      isCanvas: false,
      // 节点是否可以移动
      isNodeMove: false,
      // 画布样式
      classStyleL: "",
      classStyleP: "",
      // 连线输出点
      outputPoint: {},
      // 连接点信息
      nodeCoordinate_ING: {},
      inputPointCoordinates: [],
      outputPointCoordinates: [],
      // 路径列表
      pathsListData: [],
    };
  },

  computed: {
    // 将parent的数据进行存储
    nodeData_ING() {
      let obj = { ...this.nodeDatas[this.nodeDatas.length - 1] };
      return obj;
    },
  },

  created() {
    this.canvasHeight = this.canvasHeight * window.devicePixelRatio;
    this.canvasWidth = this.canvasWidth * window.devicePixelRatio;
  },

  mounted() {
    // 控制全选事件
    document.getElementById("main").onselectstart = function () {
      return false;
    };

    // 获取canvas元素
    this.canvasElement = document.getElementById("main");

    // let isMac = /macintosh|mac os x/i.test(navigator.userAgent);
    // if (isMac) {
    //   // 保持画布的分辨率与显示屏幕相同
    //   this.canvasElement.width =
    //     (this.canvasElement.clientWidth * window.devicePixelRatio) / 2;
    //   // 保持画布的分辨率与显示屏幕相同
    //   this.canvasElement.height =
    //     (this.canvasElement.clientHeight * window.devicePixelRatio) / 2;
    // } else {
    //   // 保持画布的分辨率与显示屏幕相同
    //   this.canvasElement.width =
    //     this.canvasElement.clientWidth * window.devicePixelRatio;
    //   // 保持画布的分辨率与显示屏幕相同
    //   this.canvasElement.height =
    //     this.canvasElement.clientHeight * window.devicePixelRatio;
    // }

    // 判断是否支持canvas
    if (this.canvasElement.getContext) {
      this.cxt = this.canvasElement.getContext("2d"); //2d用单引用括起来
      this.cxt.scale(window.devicePixelRatio, window.devicePixelRatio); //设置缩放比例，防止高清屏模糊
      this.cxt.font = this.fontSize * this.zoom + "px IconFont"; //  !!!设置字体， 必须是IconFont
      let icon_1 = this.iconFont("icon_1");
      this.pointWidth = this.cxt.measureText(icon_1).width;
      this.drawGird();
    }
  },

  methods: {
    canvasClick(event) {
      // TODO
      /*****  获取鼠标具体位置，判断是否在节点位置中  *****/
      let reverseArray = [...this.nodesListData];
      let node = reverseArray.reverse().find((ele) => {
        return (
          ele.x * this.zoom < event.offsetX &&
          event.offsetX < (ele.x + ele.width) * this.zoom &&
          ele.y * this.zoom < event.offsetY &&
          event.offsetY < (ele.y + ele.height) * this.zoom
        );
      });

      // 如果鼠标在节点中，且再次判断鼠标是否在删除按钮处
      if (
        node &&
        (node.x + 10) * this.zoom < event.offsetX &&
        event.offsetX < (node.x + 10 + node.fontSize) * this.zoom &&
        (node.y + 10) * this.zoom < event.offsetY &&
        event.offsetY < (node.y + 10 + node.fontSize) * this.zoom
      ) {
        this.$emit("nodeClick", node.id);
      } else if (node) {
        this.$emit("nodeClick", node);
      }
    },

    drawGird() {
      /*
        1. 设置网格的大小，gridSize用于确定网格之中的线之间的间隔
        2. 获取Canvas的宽度width、高度height，用于计算x轴、y轴需要绘画的条数
        3. 采用遍历的方式，绘画x轴的线条
        4. 采用遍历的方式，绘画y轴的线条
      */

      // 1. 设置网格大小
      let girdSize = 12 * this.zoom;

      // 2. 获取Canvas的width、height
      let CanvasWidth = this.cxt.canvas.width;
      let CanvasHeight = this.cxt.canvas.height;

      // 3. 采用遍历的方式，绘画x轴的线条
      let xLineTotals = Math.floor(CanvasHeight / girdSize); // 计算需要绘画的x轴条数
      for (let i = 0; i < xLineTotals; i++) {
        this.cxt.beginPath(); // 开启路径，设置不同的样式
        this.cxt.moveTo(0, girdSize * i - 0.5); // -0.5是为了解决像素模糊问题
        this.cxt.lineTo(CanvasWidth, girdSize * i - 0.5);
        this.cxt.strokeStyle = "#f1f1f1"; // 设置每个线条的颜色
        this.cxt.stroke();
      }

      // 4.采用遍历的方式，绘画y轴的线条
      let yLineTotals = Math.floor(CanvasWidth / girdSize); // 计算需要绘画y轴的条数
      for (let j = 0; j < yLineTotals; j++) {
        this.cxt.beginPath(); // 开启路径，设置不同的样式
        this.cxt.moveTo(girdSize * j, 0);
        this.cxt.lineTo(girdSize * j, CanvasHeight);
        this.cxt.strokeStyle = "#f1f1f1"; // 设置每个线条的颜色
        this.cxt.stroke();
      }
    },

    iconFont(id) {
      return document.getElementById(id).textContent;
    },

    // 基本路基绘制
    drawCanvasPath() {
      /*****  绘制线段  *****/
      this.pathDatas.forEach((inx) => {
        // 获取输入点坐标数据
        let inCoordinate_ING = this.inputPointCoordinates.find((a) => {
          return inx.input == a.input[0].id;
        });
        // 获取输出点坐标数据
        let outCoordinate_ING = this.outputPointCoordinates.find((a) => {
          return inx.output == a.output[0].id;
        });

        // 绘制
        this.drawArrow(
          outCoordinate_ING.pointCoordinates[0].x + this.fontSize * this.zoom,
          outCoordinate_ING.pointCoordinates[0].y +
            (this.fontSize / 2) * this.zoom,
          inCoordinate_ING.pointCoordinates[0].x,
          inCoordinate_ING.pointCoordinates[0].y + 4 * this.zoom
        );
        this.pathsListData.push(inx);
      });

      this.$emit("changePathData", this.pathsListData);
    },

    // 基本节点绘制
    drawCanvasNode() {
      this.nodeDatas.forEach((ele) => {
        let scaleHeight = (2 * 35 + 40) * this.zoom;
        // 字体大小
        let fontSize = this.fontSize * this.zoom;
        // 根据标题长度计算整体长度;
        let textWidth =
          this.cxt.measureText(ele.allName).width + 60 < 170 - fontSize
            ? 170 - fontSize
            : this.cxt.measureText(ele.allName).width + 80;
        // 加上一个fontsize的身位，是因为前方添加了一个叉
        let scaleWidth = (textWidth + fontSize) * this.zoom;
        // 圆角角度
        let scaleRadius = 12 * this.zoom;

        // 启动绘制函数
        this.fillRoundRect(
          this.cxt,
          ele.x * this.zoom,
          ele.y * this.zoom,
          scaleWidth,
          scaleHeight,
          scaleRadius,
          /*optional*/
          "#fff",
          /*optional*/
          "#e4e4e4",
          ele.color,
          ele.allName,
          fontSize
        );

        // 保存绘制参数
        let obj = {
          id: ele.id,
          // 节点的横坐标
          x: ele.x,
          // 节点的纵坐标
          y: ele.y,
          // 节点的初始宽度
          width: scaleWidth,
          // 节点的初始高度
          height: 2 * 35 + 40,
          // 节点的名称
          name: ele.name,
          // 自定义节点名称
          allName: ele.allName,
          // 节点的颜色
          color: ele.color,
          // 节点的圆角角度
          radius: scaleRadius,
          // 节点的字体大小
          fontSize: fontSize,
        };

        // 计算输入点坐标，（暂时为一个，所以不做循环，直接写死）
        let inCoordinate_ING = [
          {
            x: ele.x + 10 * this.zoom,
            y: ele.y + 50 * this.zoom,
            sideChief: this.fontSize * this.zoom,
          },
        ];

        // 保存输入点的位置信息
        this.inputPointCoordinates.push({
          // 输入点的名称
          name: ele.name,
          // 输入点的idd
          id: ele.id,
          // 输入点的代号，分析多个输入点的情况
          input: ele.input,
          // 输入点的坐标（通过计算）
          pointCoordinates: [...inCoordinate_ING],
        });

        // 计算输入点坐标，（暂时为一个，所以不做循环，直接写死）
        let outCoordinate_ING = [
          {
            x: ele.x + scaleWidth - 26 * this.zoom,
            y: ele.y + 80 * this.zoom,
            sideChief: this.fontSize * this.zoom,
          },
        ];

        // 保存输出点的位置信息
        this.outputPointCoordinates.push({
          // 输出点的名称
          name: ele.name,
          // 输入点的id
          id: ele.id,
          // 输出点的代号，分析多个输出点的情况
          output: ele.output,
          // 输出点的坐标（通过计算）
          pointCoordinates: [...outCoordinate_ING],
        });
        this.nodesListData.push(obj);
      });

      this.$emit("changeNodeData", this.nodesListData);
    },
  },

  watch: {
    nodeDatas: {
      //深度监听，可监听到对象、数组的变化
      handler() {
        // 整体绘制之前，先要保证整个画布是空白的
        this.nodesListData = [];
        this.pathsListData = [];
        this.outputPoint = {};
        // 连接点信息
        this.nodeCoordinate_ING = {};
        this.inputPointCoordinates = [];
        this.outputPointCoordinates = [];
        // 清空画布
        this.cxt.clearRect(
          0,
          0,
          this.canvasElement.width,
          this.canvasElement.height
        );
        // 绘制背景
        this.drawGird();
        // 绘制节点
        this.drawCanvasNode();
        // 绘制线段
        this.drawCanvasPath();
      },
      deep: true,
    },
  },
};
</script>

