    transform: translateX(50%);
    z-index: 999;
    display: flex;
    width: 90%;
    justify-content: space-between;
    .title-left {
      display: flex;
    }
    .circular {
      height: 2rem;
      width: 2rem;
      font-weight: 600;
      background: #fff;
      box-shadow: 0.125rem 0.125rem 1.25rem rgba(25, 25, 25, 0.1);
      border-radius: 1.5625rem;
      text-align: center;
      line-height: 2rem;
      margin-right: 0.625rem;
      cursor: pointer;
    }
  }
  #main {
    width: 3000px;
    height: 2500px;
    background: #fff;
  }

  .iconfont-none {
    font-family: "iconfont" !important;
    font-size: 1rem;
    font-style: normal;
    display: none;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.0125rem;
    -moz-osx-font-smoothing: grayscale;
  }
  .iconfont {
    font-family: "iconfont" !important;
    font-size: 1rem;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.0125rem;
    -moz-osx-font-smoothing: grayscale;
  }
