<template>
  <div class="MAF">
    <div class="title">Dimension Reduction</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin: 0.625rem 0" />
    <p class="tip">Local upload:</p>
    <div class="action">
      <input
        type="file"
        id="files"
        class="upload-btn"
        ref="files"
        multiple
        @change="handleFilesUpload"
      />
      <span type="text" class="file-input-trigger" @click="updateFile"
        >upload</span
      >
      <div class="content">
        <div
          class="file"
          v-for="(file, index) in params.localFiles"
          :key="index"
        >
          <div class="name">{{ file.name }}</div>
          <a-icon
            class="choice"
            type="close-circle"
            @click="removeFile(1, index)"
          />
        </div>
      </div>
    </div>
    <p class="tip">Library upload:</p>
    <div class="action">
      <span type="text" class="file-input-trigger" @click="libraryTable"
        >upload</span
      >
      <div class="content">
        <div
          class="file"
          v-for="(file, index) in params.libraryFiles"
          :key="index"
        >
          <div class="name">{{ file.name }}</div>
          <a-icon
            class="choice"
            type="close-circle"
            @click="removeFile(2, index)"
          />
        </div>
      </div>
    </div>
    <LibraryTable
      :name="name"
      @DimensionReductionselections="DimensionReductionselections"
    />
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";
import LibraryTable from "./components/librarytable.vue";
export default {
  name: " DimensionReduction",
  props: {
    nodeId: {
      type: Number,
    },
  },
  data() {
    return {
      params: {
        data: {},
        libraryFiles: [],
        localFiles: [],
      },
      name: "", //将自己的name给table组件
    };
  },
  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    // this.params.MAFNumber = this.messageTags;
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach((element) => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },
  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },
  components: {
    changeName,
    LibraryTable,
  },
  mounted() {
    this.recoverData(this.nodeId);
  },
  methods: {
    updateFile() {
      this.$refs.files.click();
    },
    libraryTable() {
      this.name = "DimensionReduction";
      this.$store.commit("changelibraryTable", true);
    },
    handleFilesUpload() {
      let uploadedFiles = this.$refs.files.files;
      for (let i = 0; i < uploadedFiles.length; i++) {
        this.params.localFiles.push(uploadedFiles[i]);
      }
    },
    removeFile(index, file) {
      if (index == 1) {
        this.params.localFiles.splice(file, 1);
      } else {
        this.params.libraryFiles.splice(file, 1);
      }
    },
    DimensionReductionselections(e, libraryid) {
      this.params.libraryFiles = e;
      this.params.libraryFiles.forEach((item) => {
        item["libraryid"] = libraryid;
      });
    },
    //复用函数
    recoverData(a) {
      this.workflowNodeInfo.forEach((element) => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {
            this.params.localFiles = element.params.localFiles;
            this.params.libraryFiles = element.params.libraryFiles;
          }
        }
      });
    },
  },
};
</script>

