<template>
  <div class="Classify">
    <div class="title">Classify</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin: 0.625rem 0" />
    <div class="title-second">model:</div>
    <div class="content-data">
      <a-select :default-value="defaultValue_M" size="small" style="width: 100%" @change="handleChange_M">
        <a-select-option v-for="items in dataList" :key="items.name"> {{ items.name }} </a-select-option>
      </a-select>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import changeName from './components/changeName';

export default {
  name: 'Classify',

  props: {
    nodeId: {
      type: Number
    }
  },

  data() {
    return {
      params: {},
      defaultValue_M: "",
      dataList: []
    };
  },

  computed: {
    ...mapState({
      projectId: state => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: state => state.workflowNode.workflowNodeInfo // 暂存每个已有的节点信息
    })
  },

  components: {
    changeName
  },

  created() {
    this.recoverData(this.nodeId);
  },

  mounted() {
    this.$progress.start();
    this.$ProjectModels.getModelModels().then(res => {
      this.dataList = res.data;
      this.$progress.finish();
    });
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex;
    this.params.modelName = this.defaultValue_M;
    let obj = Object.assign({}, this.params);
    let arr = Array.from(this.workflowNodeInfo);
    arr.forEach(element => {
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit('postworkflowNodeData', arr);
  },

  methods: {
    handleChange_M(value){
      this.defaultValue_M = value;
    },

    // 复用函数，复原数据
    recoverData(a) {
      this.workflowNodeInfo.forEach(element => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {
            this.defaultValue_M = element.params.modelName;
          }
        }
      });
    }
  }
};
</script>

