<template>
  <div class="InputGeneList">
    <div class="title">Input Gene List</div>

    <a-divider style="margin: 0.625rem 0" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin: 0.625rem 0" />

    <div class="title-second">Genes List:</div>

    <div class="content-data">
      <div class="upload-tags">
        <p
          style="
            font-style: italic;
            margin-bottom: 0.9375rem;
            fonts-size: 0.75rem;
          "
        >
          Try to copy &nbsp;
          <span style="background: #f1f1f1"> list1 , list2 </span> &nbsp; into
          the input box , Then hit enter.(Please use English comma!)
        </p>
        <div
          class="textarea"
          @click="getFocus"
          style="max-height: 37.5rem; overflow: auto"
        >
          <template v-for="(tag, index) in messageTags">
            <a-tag
              :key="tag"
              :closable="index == index"
              :afterClose="() => handleClose(tag)"
              >{{ tag }}</a-tag
            >
          </template>
          <div class="add-tag">
            <span v-show="params.genesList.length != 0"
              >{{ params.genesList }}&nbsp;</span
            >
          </div>
        </div>
        <input
          class="input"
          style="border-bottom: 0.1rem solid #000; padding-bottom: 0.3rem"
          ref="message"
          type="text"
          placeholder="请输入"
          v-model="params.genesList"
          @keyup.enter="handleMessageData"
          @keyup.delete="handleMessageDelete"
        />
      </div>
      <p class="taxmony">taxonomy</p>
      <a-radio-group
        v-model="params.taxonomy"
        :options="options"
        @change="onChange"
      />
      <p class="taxmony">keyType</p>
      <a-radio-group
        v-model="params.keyType"
        :options="keyType"
        @change="onChange1"
      />
    </div>
    <a-divider dashed style="margin: 0.625rem 0" />
    <div class="filter">
      <div class="title-second">
        在你提交一个进程之后，根据你的结果自动生成一个筛选器
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import changeName from "./components/changeName";
const options = [
  { label: "mouse", value: "mouse" },
  { label: "human ", value: "human " },
];
const keyType = [
  { label: "SYMBOL", value: "SYMBOL" },
  { label: "ENSEMBL", value: "ENSEMBL" },
  { label: "ENTREZID", value: "ENTREZID" },
];
export default {
  name: "InputGeneList",

  props: {
    nodeId: {
      type: Number,
    },
  },

  data() {
    return {
      options,
      keyType,
      messageTagData: "",
      messageTags: [],
      params: {
        genesList: [],
        taxonomy: "",
        keyType: "",
      },
      id: "",
    };
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: (state) => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    }),
  },

  components: {
    changeName,
  },

  mounted() {
    this.recoverData(this.nodeId);
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex
    // 数据格式整理
    let obj = Object.assign({}, this.params);
    // 元数据格式暂改
    let arr = Array.from(this.workflowNodeInfo);
    // 溯源
    arr.forEach((element) => {
      // 数据更新
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit("postworkflowNodeData", arr);
  },

  methods: {
    // 处理自定义写入实验数据
    handleMessageData() {
      let arr = this.params.genesList.split(","); //字符分割
      arr.forEach((element) => {
        if (element === "" || this.params.genesList.indexOf(element) > -1) {
          return;
        }
        this.params.genesList.push(element);
      });
      // 输入完成之后清除输入框
      this.messageTagData = "";
    },

    handleClose(removedTag) {
      this.params.genesList.splice(
        this.params.genesList.indexOf(removedTag),
        1
      );
    },

    // 使用delete按键删除事件
    handleMessageDelete() {
      if (this.messageTagData === "") {
        // 输入框中没有任何数据的时候，删除数组中的最后一个元素。改变原数组！
        this.params.genesList.pop();
      }
    },

    // 点击聚焦事件
    getFocus() {
      this.$refs.message.focus();
    },

    // 复用函数，复原数据
    recoverData(a) {
      this.workflowNodeInfo.forEach((element) => {
        if (element.id == a) {
          if (Object.keys(element.params).length !== 0) {
            this.params.genesList = element.params.genesList;
          }
        }
      });
    },
    onChange(e) {
      this.params.taxonomy = e.target.value;
    },
    onChange1(e) {
      this.params.keyType = e.target.value;
    },
  },
};
</script>

