      <div class="title-second">Mutation List:</div>
      <a-textarea
        v-model="params.mutation_list"
        auto-size
      />
      <div class="title-second">Choose data:</div>
      <a-radio-group v-model="params.inputFiles">
        <a-radio value="union">
