<!--
* @componmentName introduction
* @module "./components/introduction"
* @fileDescription 工具首页的左侧列表组件，
* @author li
* @date 2020年8月16日
* @param [Array] {projectData} 项目信息数组
* @param [String] {loading} 一个布尔值，是否开始加载动画
* @example 调用示例
* <projectList :projectData="projectData" :loading="loading"></projectList>
 -->

<template>
  <div class="projectList">
    <div class="contaier">
      <div class="header">
        <div class="user-info">
          <p>Project list</p>
        </div>
      </div>

      <div class="data-content">
        <ul class="data-table">
          <div v-if="loading" style="padding: 0 3.125rem 0 3.125rem">
            <a-skeleton
              avatar
              :paragraph="{ rows: 2 }"
              :loading="loading"
              active
            />
            <div style="margin-top: 2.5rem"></div>
            <a-skeleton
              avatar
              :paragraph="{ rows: 2 }"
              :loading="loading"
              active
            />
            <div style="margin-top: 2.5rem"></div>
            <a-skeleton
              avatar
              :paragraph="{ rows: 2 }"
              :loading="loading"
              active
            />
          </div>

          <!-- 时间筛选器 -->
          <div v-else-if="isNoData">
            <a-menu
              mode="inline"
              :open-keys="openKeys"
              style="width: 100%"
              @openChange="onOpenChange"
            >
              <a-sub-menu v-for="items in filterData" :key="items.years">
                <span slot="title">
                  <a-icon type="appstore" />
                  <span style="font-size: 1.125rem; font-weight: 550">{{
                    items.name
                  }}</span>
                </span>
                <a-sub-menu v-for="item in items.months" :key="item.month">
                  <span slot="title">
                    <span style="font-weight: 550">{{ item.name }}</span>
                  </span>
                  <a-menu-item
                    style="padding-left: 5rem"
                    v-for="values in item.data"
                    :key="values.idx"
                    @click="handleProject(values.projectId, values.name)"
                    >{{ values.name }}</a-menu-item
                  >
                </a-sub-menu>
              </a-sub-menu>
            </a-menu>
          </div>

          <div v-else class="no-data">NO DATA</div>
        </ul>
        <div class="project-num">
          Projects:
          <span v-if="projectData.length == 0">null</span>
          <span v-else>{{ projectData.length }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "projectList",
  props: {
    projectData: {
      type: Array,
    },
    loading: {
      type: Boolean,
    },
  },
  data() {
    return {
      // 临时承载空数组
      arr: [],
      // 是否显示筛选列表
      isNoData: true,
      // 筛选列表数组
      filterData: [],
      // 列表key
      rootSubmenuKeys: ["sub1", "sub2", "sub4"],
      // 显示敞开列表
      openKeys: ["sub1"],
      searchData: [],
    };
  },

  watch: {
    // 数据监听，根据父组件传递的数据对数据按照年份月份事件排序
    projectData(proData) {
      this.arr = proData; // 将父组件的信息通过监听传递给组件，以便于更改。
      // 数据按照年份进行归类
      var yearContainer = {}; // 针对键year进行归类的容器
      this.arr.forEach((item) => {
        yearContainer[item.createDate.substring(0, 4)] =
          yearContainer[item.createDate.substring(0, 4)] || []; // 一个对象中不存在多个重复的键值
        yearContainer[item.createDate.substring(0, 4)].push(item); // 向相同的键值中push数据
      });

      let yearsArr = []; // 最终数据储存容器
      for (var item in yearContainer) {
        yearsArr.push({
          years: item, // 年份标识
          name: item, // 将提取的年份信息，编辑成对象数组，并更改信息。
          _months: yearContainer[item], // 携带所有当前年份的数据
          months: [], // 储存筛选后的月份信息
          monthsContainer: {}, // 针对键值months进行归类的容器
        });
      }
      yearsArr.forEach((yearItem) => {
        // 归类月份信息
        //将key值换成int类型，能够按照大小排序
        yearItem._months.forEach((monthsItem) => {
          yearItem.monthsContainer[
            parseInt(monthsItem.createDate.substring(5, 7))
          ] =
            yearItem.monthsContainer[
              parseInt(monthsItem.createDate.substring(5, 7))
            ] || [];
          yearItem.monthsContainer[
            parseInt(monthsItem.createDate.substring(5, 7))
          ].push(monthsItem);
        });
      });
      yearsArr.forEach((yearItem) => {
        // 整理月份信息
        for (var item in yearItem.monthsContainer) {
          yearItem.months.push({
            month: yearItem.years + item,
            name: item, // 将提取的月份信息，编辑成对象数组，并更改信息。
            data: yearItem.monthsContainer[item],
          });
        }
      });
      // 将筛选好的数组赋给data函数的装载容器
      this.filterData = yearsArr;
    },
  },

  mounted() {
    if (sessionStorage.getItem("workflowState")) {
      let obj = JSON.parse(sessionStorage.getItem("workflowState"));
      this.workflowId = obj.workflowId;
      this.nodesData = obj.nodesData;
      this.$store.commit("getWorkflowId", obj.workflowId);
    }
  },

  methods: {
    onOpenChange(openKeys) {
      const latestOpenKey = openKeys.find(
        (key) => this.openKeys.indexOf(key) === -1
      );
      if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
        this.openKeys = openKeys;
      } else {
        this.openKeys = latestOpenKey ? [latestOpenKey] : [];
      }
    },

    handleProject(e, f) {
      let obj = {
        id: e,
        name: f,
      };
      this.$store.commit("getProjectId", obj);
      sessionStorage.setItem("getProjectId", JSON.stringify(obj));
      this.$ProjectModels.getAnalysesModels().then((res) => {
        let searchData = [];
        if (e == "") {
          this.searchData = res.data;
        } else {
          for (let i in res.data) {
            if (res.data[i].project.id == e) {
              searchData.push(res.data[i]);
            }
          }
        }
        this.$emit("projectList", searchData);
      });
    },
  },
};
</script>

