<template>
  <div class="CORRELATION">
    <div class="title">CORRELATION</div>
       
    <a-divider style="margin:0.625rem 0;" />
    <change-name :nodeId="nodeId"></change-name>

    <a-divider dashed style="margin:0.625rem 0;" />
    <div class="title-second">Method:</div>
    <div class="content-data">
      <a-radio-group @change="onChange" v-model="value">
        <a-radio :value="1">Pearson</a-radio>
        <a-radio :value="2">Spearman</a-radio>
      </a-radio-group>
    </div>
  
  </div>
</template>

<script>
import { mapState } from 'vuex';
import changeName from "./components/changeName";

export default {
  name: "CORRELATION",
    
  props:{
    nodeId:{
      type:Number
    }
  },

  data(){
    return{
      value:1,
      method:'Pearson',
      params:{}
    }
  },

  computed: {
    ...mapState({
      projectId: state => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: state => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
    })
  },
  
  components:{
    changeName
  },

  mounted(){
    this.recoverData(this.nodeId)
  },

  beforeDestroy() {
    // 当组件销毁的时候，保存信息到vuex;
    // 重新记录当前单选框记录
    if (this.value === 1) {
      this.params.method = 'Pearson'
    } else {
      this.params.method = 'Spearman'
    }
    this.params.method = this.method;
    let obj = Object.assign({}, this.params);
    let arr = Array.from(this.workflowNodeInfo);
    arr.forEach(element => {
      if (element.id == this.nodeId) {
        element.params = obj;
      }
    });
    // 记录到vuex
    this.$store.commit('postworkflowNodeData', arr);
  },

  methods:{
    onChange(e){
      // console.log(e.target.value)
      if(e.target.value == 1){
        this.method = 'Pearson'
      }else{
        this.method = 'Spearman'
      }
    },

     // 复用函数，复原数据
    recoverData(a){
      this.workflowNodeInfo.forEach(element => {
        if (element.id == a) {
          if(Object.keys(element.params).length !== 0){
            if (element.params.method == "Pearson") {
              this.value = 1;
            } else {
              this.value = 2;
              this.method = "Spearman"
            }
          }
        }
      });
    }
  }
};
</script>

