<template>
  <div class="plot">
    <!-- 图表和表格 -->
    <div class="plot-show">
      <div class="plot-box">
        <div class="box-title">Chart display</div>
      </div>
      <div>
        <div class="box-title">Chart modification</div>
      </div>
    </div>
    <!-- 结束 -->

    <!-- 图表和表格 -->
    <div class="plot-show">
      <div class="plot-box">
        <!-- echart组件 -->
        <scatter-plot
          :plotData="plotData"
          :secondData="secondData"
        ></scatter-plot>
      </div>
      <div class="slider">
        <!-- plot 修改slider -->
        <modify-scatter
          :plotData="plotData"
          @secondDraw="secondDraw"
        ></modify-scatter>
      </div>
    </div>
    <!-- 结束 -->
  </div>
</template>

<script>
// echart组件
import ScatterPlot from "./plotComponents/ScatterPlot";
import modifyScatter from "./modifyPlotComponents/modifyScatter";
import { mapState } from "vuex";
// 颜色数组
import colors from "../../../../../utils/colors";

export default {
  name: "plot",

  components: {
    ScatterPlot,
    modifyScatter,
  },

  data() {
    return {
      // yanse
      colors: [],
      // 绘图数据
      plotData: [],
      secondData: [],
    };
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      workflowId: (state) => state.workflowId.workflowId,
      resultNodeId: (state) => state.workflowId.resultNodeId,
      stateName: (state) => state.stateName.stateName,
    }),
  },

  created() {
    // 获取颜色
    this.colors = colors.colors;
    // 读取记录的ResultNodeId，以便渲染图
    this.$store.commit(
      "getResultNodeId",
      sessionStorage.getItem("getResultNodeId")
    );
  },

  mounted() {
    if (this.plotData.length == 0) {
      let id = this.projectId;
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      this.getPlotData(workflowId, id, nodeId, name);
    }
  },

  methods: {
    // 复用函数 （图和表数据）
    getPlotData(workflowId, id, nodeId, name) {
      let isFile = false; // 请求图表的坐标数据，所以直接设置为false
      let ispng = false;
      this.$progress.start();
      this.$ProjectModels
        .getPlotResultModelsF(workflowId, id, nodeId, name, isFile, ispng)
        .then((val) => {
          this.plotData = val.data;
          this.$store.commit("getStateName", name); // 储存点击的node name；
          this.$store.commit("pushCollects", this.plotData); //储存数据在vuex
          this.workflowName = val.data.name;
          if (val.data.length == 0) {
            this.$message.warning("No data");
          }
          this.$progress.finish();
        });
    },

    // 父组件二次绘制任务收集
    secondDraw(e) {
      this.secondData = e;
    },
  },

  watch: {
    resultNodeId() {
      let id = this.projectId;
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      this.getPlotData(workflowId, id, nodeId, name);
    },
    workflowId() {
      let id = this.projectId;
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      this.getPlotData(workflowId, id, nodeId, name);
    },
  },
};
</script>

