    getpathDatas() {
      this.pathDatas.forEach((ele) => {
        this.dataForm.edges.push({ input: ele.input, output: ele.output });
      });
    },
    //提交事件
    dataFormUp() {
      // 销毁组件保存信息
      this.$store.commit("getNodeId", 10000000);
      this.$store.commit("changeCity", 100000); //  设置nodeId到vuex
      this.workflowChartId = 100000;
      this.$emit("dataFormUp", 0);
      setTimeout(() => {
        // 将外部传入的函数的执行放在setTimeout中
        this.dataForm.nodes = this.nodeDatas;
        this.workflowNodeInfo.forEach((inx) => {
          if (inx.name === "aggregation") {
            this.fileList = inx.params.fileList;
          }
          // 获取到最新的节点信息列表
          this.dataForm.nodes.forEach((val) => {
            // 获取节点状态
            if (inx.id === val.id) {
              if (inx.name === "aggregation") {
                let obj = {
                  params: {
                    aggregation: inx.params.aggregation,
                    pairedRatio: inx.params.pairedRatio,
                  },
                };
                val.params = Object.assign({}, obj); // 将用户添加的节点信息复制到dataForm中
                val.allName = inx.allName; // 将用户修改的节点名称复制到dataForm中
              } else {
                val.params = Object.assign({}, inx.params); // 将用户添加的节点信息复制到dataForm中
                val.allName = inx.allName; // 将用户修改的节点名称复制到dataForm中
              }
            }
          });
        });
        // 获取连线信息
        (this.dataForm.edges = []), this.getpathDatas();
        // 获取project id
        let id = this.projectId;
        // 获取workflow name 如果不存在就变为'Undefined'
        if (
          this.workflowName == "Unnamed Workflow" ||
          this.workflowName == ""
        ) {
          this.dataForm.name = "Undefined";
        } else {
          this.dataForm.name = this.workflowName;
        }
        this.dataForm.workflowId = this.workflowId;
        let data = {
          DAG: {},
        };
        data.DAG = this.dataForm;
        this.$progress.start();
        let formData = new FormData();
        //循环添加到formData中
        if (this.fileList.length !== 0) {
          this.fileList.forEach(function (file) {
            formData.append("files", file, file.name);
          });
        }
        formData.append("DAG", JSON.stringify(data.DAG));
        this.$ProjectModels
          .postCommitWorkflowModels(id, formData)
          .then((res) => {
            if (res.data.length == 0) {
              this.$message.warning("No data");
            } else {
              this.$message.success("Submitted successfully", 3);
            }
            this.$progress.finish();
          });
        console.log(JSON.stringify(data));
      }, 10);
    },

    // 保存workflow
    dataFormConserve() {
      this.workflowNodeInfo.forEach((res) => {
        // 获取到最新的节点信息列表
        this.dataForm.nodes.forEach((ele) => {
          // 获取节点状态
          if (res.id === ele.id && ele.name !== "GQM") {
            // 当上传的模板中存在GQM的时候，拒绝上传GQM的params信息。
            ele.params = Object.assign({}, res.params);
            // 将用户添加的节点信息复制
          }
        });
      });

      let obj = {
        color: "",
        nodes: this.nodeDatas,
        paths: this.pathDatas,
        startNode: {},
        work_id: this.workflowName,
        workflowId: 1,
      };
      this.$progress.start();
      this.$ProjectModels.postWorkflowTemplateModels(obj).then((ele) => {
        if (ele.status == 201) {
          this.$message.success("Saved successfully.", 3);
          this.visible_1 = false;
        } else {
          this.$message.warning("Save failed." + ele.status, 3);
          this.visible_1 = false;
        }
        this.$progress.finish();
      });
    },

    //删除事件
    pathDelete() {
      this.nodeDatas = [];
      this.pathDatas = [];
      // 使用命名空间对不同module中同名函数进行操作！
      this.$store.commit("workflow/getDefaultState");
      this.$store.commit("getDefaultState");
    },

    // 开启workflow名称输入框
    showInputName() {
      this.inputNameShow = true;
      this.workflowName = "";
      this.$refs.inputName.focus();
      this.workflowId = "Undefined";
    },

    // 数据更新
    changeNodeData(data) {
      this.nodeDatas.forEach((ele) => {
        data.forEach((inx) => {
          if (ele.id == inx.id) {
            ele.x = inx.x;
            ele.y = inx.y;
          }
        });
      });
    },

    // 数据更新
    changePathData(data) {
      this.pathDatas = data;
    },
    sorted(e) {
      if (e == "omics") {
        this.Sorted = "omics";
      } else {
        this.Sorted = "function";
      }
    },
  },

  watch: {
    dataset() {
      // 公共和自定义模板渲染
      if (this.dataset != null) {
        this.nodeDatas = this.dataset.nodes; // 更新绘图数组,渲染视图节点
        this.pathDatas = this.dataset.paths; // 更新绘图数组,渲染视图连接
        this.workflowName = this.dataset.workflowName; // 更新绘图数组,渲染视图连接
        this.workflowId = this.dataset.workflowId; // 更新绘图数组,渲染视图连接
      }
    },

    inputNameShow() {
      if (this.workflowName == "") {
        this.workflowName == "Unnamed Workflow";
      }
    },

    workflowNodeInfo(e) {
      e.forEach((ele) => {
        this.nodeDatas.forEach((inx) => {
          if (ele.id === inx.id) {
            inx.allName = ele.allName;
          }
        });
      });
    },
  },
};
</script>

