
<template>
  <!-- 该组件用于类似于FAGL节点的预览模式，可以提供多选框，将数据重新提交给服务器 -->
  <div class="checkboxTable">
    <div style="margin-bottom: 16px">
      <a-button
        type="primary"
        :disabled="!hasSelected"
        :loading="loading"
        @click="start"
        >clear</a-button
      >
      <span style="margin-left: 8px">
        <template v-if="hasSelected">{{
          `Selected ${selectedRowKeys.length} items`
        }}</template>
      </span>
    </div>
    <a-table
      :row-selection="{
        selectedRowKeys: selectedRowKeys,
        onChange: onSelectChange,
      }"
      :columns="columns"
      :data-source="listData"
      :scroll="scroll"
      rowKey="ID"
    ></a-table>

    <!-- <div class="slide-right">
      <div class="sumit-data">
        <h1 style="font-size:1.125rem;font-weight:bold">Resubmit data modification</h1>
        <button @click="returnData">submit</button>
        <p style="color:#808080;font-size:0.875rem">To select the data on the left before submitting and reanalysing</p>
      </div>
      <div class="check-state">
        <h1 style="font-size:1.125rem;font-weight:bold">Resubmit data modification</h1>
        <button @click="checkState">Get task</button>
        <div class="demo-infinite-container" v-infinite-scroll="handleInfiniteOnLoad" :infinite-scroll-distance="10">
          <a-list :dataSource="stateData">
            <a-list-item slot="renderItem" slot-scope="item">
              <a-list-item-meta :description="item.submmit_time">
                <a slot="title" class="state-button" @click="workList(item.taskId)">download</a>
              </a-list-item-meta>
            </a-list-item>
          </a-list>
        </div>
        <div v-if="loading_1" class="demo-loading-container">
          <a-spin />
        </div>
        <p style="color:#808080;font-size:0.875rem">得到任务列表之后，点击下载获取文件</p>
      </div>
    </div> -->
  </div>
</template>


<script>
import { mapState } from "vuex";
import infiniteScroll from "vue-infinite-scroll";

export default {
  name: "checkboxTable",

  data() {
    return {
      // 数据
      listData: [],
      listHeader: [],
      columns: [],
      selectedRowKeys: [], // Check here to configure the default column
      loading: false,
      loading_1: false,
      scroll: {},
      stateData: [],
      // fileNames
      fileNames: [],
    };
  },

  directives: {
    infiniteScroll,
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      workflowId: (state) => state.workflowId.workflowId,
      resultNodeId: (state) => state.workflowId.resultNodeId,
      stateName: (state) => state.stateName.stateName,
    }),
    hasSelected() {
      return this.selectedRowKeys.length > 0;
    },
  },

  watch: {
    resultNodeId() {
      this.getFileData();
    },
  },

  mounted() {
    this.getFileData();
  },

  methods: {
    start() {
      this.loading = true;
      // ajax request after empty completing
      setTimeout(() => {
        this.loading = false;
        this.selectedRowKeys = [];
      }, 1000);
    },
    onSelectChange(selectedRowKeys) {
      //   window.console.log('selectedRowKeys changed: ', selectedRowKeys);
      this.selectedRowKeys = selectedRowKeys;
    },

    // 请求文件所有名
    getFileData() {
      let workflowId = this.workflowId;
      let nodeId = sessionStorage.getItem("getResultNodeId");
      let name = this.stateName;
      let id = this.projectId;
      this.$progress.start();
      this.$ProjectModels
        .getTaskNodeModels(workflowId, id, nodeId, name)
        .then((inx) => {
          this.fileNames = inx.data;
          if (inx.data.length == 0) {
            this.$message.warning("No task");
          }
          if (this.projectId !== null && this.stateName !== "") {
            this.getData();
          }
          this.$progress.finish();
        });
    },

    returnData() {
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let id = this.projectId;
      let plotType = this.fileNames[0].match(/(\S*)_/)[1];
      let data = {
        params: {
          block: this.selectedRowKeys,
          colors: [],
        },
      };
      this.$progress.start();
      this.$ProjectModels
        .postPlotTaskModels(workflowId, id, nodeId, plotType, data)
        .then((res) => {
          // window.console.log(res);
          this.weizhi = res.data;
          this.$progress.finish();
        })
        .catch((error) => {
          this.$message.error(
            "Failed to update the data. Please try again or check the network." +
              error,
            5
          );
        });
    },

    checkState() {
      // 更新任务列表
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      //   let id = this.projectId;
      this.$progress.start();
      this.$ProjectModels
        .getTaskNodeStateModels(workflowId, nodeId)
        .then((res) => {
          window.console.log(res.data.states);
          this.stateData = res.data.states;
          this.$progress.finish();
        })
        .catch((error) => {
          this.$message.error(
            "Failed to update the data. Please try again or check the network." +
              error,
            5
          );
        });
    },

    handleInfiniteOnLoad() {
      this.loading_1 = false; // 任务列表的加载动画
    },

    workList(e) {
      let that = this;
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      //   let name = this.stateName;
      let type = this.filename.match(/(\S*)_/)[1];
      let id = this.projectId;
      let taskId = e;
      this.$progress.start();
      this.$ProjectModels
        .getTaskNodePlotModels(id, workflowId, nodeId, type, taskId)
        .then((eve) => {
          if (!eve) {
            return;
          }
          var filename = this.stateName;
          let url = window.URL.createObjectURL(eve.data);
          let link = document.createElement("a");
          link.style.display = "none";
          link.href = url;
          link.setAttribute(
            "download",
            filename + "." + eve.data.type.slice(12)
          );
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
          if (eve.data.length == 0) {
            that.$message.warning("No task");
          }
          this.$progress.finish();
        })
        .catch((error) => {
          that.$message.error(
            "Failed to update the data. Please try again or check the network." +
              error,
            5
          );
        });
    }, // download文件

    // 复用函数，请求数据
    getData() {
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      let id = this.projectId;
      let isFile = false;
      let blockSize = 1;
      let blockStart = 0;
      let blockNum = 10000;
      let that = this;
      let filename = that.fileNames[0];
      this.$progress.start();
      this.$ProjectModels
        .getTableResultModelsFile(
          workflowId,
          id,
          nodeId,
          name,
          isFile,
          blockSize,
          blockStart,
          blockNum,
          filename
        )
        .then((inx) => {
          window.console.log(inx);
          let { values } = Object;
          (that.listData = values(inx.data.data)),
            (that.listHeader = inx.data.header);

          let _arr = Object.keys(that.listHeader);
          that.scroll = {
            x: _arr.length * 250 + 400,
            y: 2000,
          };
          that.listHeader.forEach((res) => {
            if (res == "geneID") {
              let obj = {
                title: res,
                dataIndex: res,
                key: res,
                ellipsis: true,
              };
              that.columns.push(obj);
            } else {
              let obj = {
                title: res,
                dataIndex: res,
                width: 250,
                key: res,
                ellipsis: true,
              };
              that.columns.push(obj);
            }
          });

          that.listData = [];
          inx.data.data.forEach((res) => {
            // console.log(res)
            let obj = res.reduce(function (result, field, index) {
              result[that.listHeader[index]] = field;
              return result;
            }, {});
            that.listData.push(obj);
          });

          if (inx.data.length == 0) {
            this.$message.warning("No task");
          }
          this.$progress.finish();
        })
        .catch((error) => {
          this.$message.error(
            "Failed to update the data. Please try again or check the network." +
              error,
            5
          );
        });
    },
  },

  destroyed() {
    this.listData = [];
  },
};
</script>
 
