<template>
  <div class="tables">
    <!-- 图表和表格 -->
    <!-- 结束 -->

    <!-- 图表和表格 -->
    <div class="table-show">
      <div class="table-box tongyong" v-if="stateName == 'FAGL'">
        <div>
          <a-button
            style="float: right"
            type="primary"
            icon="download"
            :size="size"
          >
            File download
          </a-button>
        </div>
        <checkbox-table></checkbox-table>
      </div>
      <div class="table-box tongyong" v-else>
        <div>
          <a-button
            style="float: right; margin-right: 2rem"
            type="primary"
            ghost
          >
            Send
          </a-button>
          <a-button
            style="float: right; margin-right: 1rem"
            type="primary"
            icon="download"
            :size="size"
            ghost
          >
            File download
          </a-button>
        </div>
        <!-- 表格组件 -->
        <scroll-table
          style="margin-left: 2rem"
          :dataHeap="dataHeap"
          :filterStyle="true"
        >
          <!-- 表格title展示 -->
          <template slot="tableTitle">
            <p style="font-size: 0.625rem; margin: 0.625rem">
              After filtering the following table, add one or more labels and
              add a new analysis node! ( The label is displayed on the right. :)
            </p>
          </template>
          <!-- 表格筛选条件获取 -->
          <template slot="showCondition" slot-scope="slot">
            <button
              v-show="!inputName"
              class="add-label-input spaceInRight"
              @click="showInput"
            >
              create label
            </button>
            <div class="input-name slideLeftReturn" v-show="inputName">
              <input type="text" placeholder="label name" v-model="labelName" />
              <button
                class="add-btn"
                @click="createLabel(slot.filtersCondition)"
              >
                <a-icon type="plus" />
              </button>
            </div>
          </template>

          <!-- 表格头部展示 -->
          <template slot="tableHeader" slot-scope="slot">
            <div>{{ slot.title }}</div>
          </template>
          <!-- 表格内容展示 -->
          <template slot="tableBox" slot-scope="slot">
            <div v-if="typeof slot.value == 'object'" style="display: flex">
              <div v-for="(label, key) in slot.value" :key="label">
                <a-tag :color="colors[key]">{{ label }}</a-tag>
              </div>
            </div>
            <div v-else>
              <a-tooltip
                v-if="slot.value.length > 13"
                :key="slot.value"
                :title="slot.value"
                placement="top"
              >
                <p style="white-space: nowrap">
                  {{ `${slot.value.slice(0, 13)}...` }}
                </p>
              </a-tooltip>
              <p v-else>{{ slot.value }}</p>
            </div>
          </template>
        </scroll-table>
      </div>

      <div class="slider">
        <div class="download">
          <div class="file-name" v-for="item in fileNames" :key="item.index">
            <div class="btn">
              <div class="btn-download" @click="download(item)">
                <a-button ghost icon="download" type="primary">
                  download
                </a-button>
              </div>
              <div class="btn-preview" @click="preview(item)">
                <a-icon type="eye" />
              </div>
            </div>
            <div class="text-item">
              {{ item }}
            </div>
          </div>
        </div>

        <div class="sub-project">
          <div class="label-button">
            <h1>generate new quantification matrix</h1>
            <div class="line" style="width: 88%"></div>
            <div class="cover-content">
              <div class="cover-label">
                <template v-for="(item, index) in labels">
                  <a-tooltip
                    v-if="item.name.length > 20"
                    :key="index"
                    :title="item.name"
                  >
                    <a-tag
                      :key="index"
                      closable
                      :afterClose="() => labelClose(item.name, item.labelId)"
                      :color="colors[index]"
                      >{{ `${item.name.slice(0, 20)}...` }}</a-tag
                    >
                  </a-tooltip>
                  <a-tag
                    v-else
                    :key="index"
                    closable
                    :afterClose="() => labelClose(item.name, item.labelId)"
                    :color="colors[index]"
                    >{{ item.name }}</a-tag
                  >
                </template>
              </div>
            </div>
            <div style="margin: 0rem 1.875rem">
              使用新的筛选后的数据添加新的任务
              <button class="run-task" @click.once="submitData">running</button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 结束 -->
  </div>
</template>

<script>
// table组件
import scrollTable from "../../../../../PublicComponment/scrollTable/scrollTable";
import checkboxTable from "./tableComponents/checkboxTable";
import { getStrWidth } from "../../../../../utils/tableWidth";
import { mapState } from "vuex";
// 颜色数组
import colors from "../../../../../utils/colors";

export default {
  name: "tables",

  components: {
    scrollTable,
    checkboxTable,
  },

  data() {
    return {
      // 表格数据
      tableData: [],
      tableHeader: [],
      tableCloumn: {},
      // yanse
      colors: [],
      // 当前节点的所有文件名
      fileNames: [],
      // 表格展示文件
      fileName: "result",
      // 是否显示添加label输入框
      inputName: "",
      // 当前label的名字
      labelName: "",
      // 存储所有label条件
      labels: [],
    };
  },

  computed: {
    // 对表格数据进行修改
    dataHeap() {
      let arr_1 = [];

      this.tableHeader.forEach((item) => {
        let obj = {};
        for (let i in this.tableCloumn) {
          if (item == i) {
            if (item == "label") {
              obj = {
                name: item,
                type: this.tableCloumn[item].type,
                width: 200,
              };
            } else {
              obj = {
                name: i,
                type: this.tableCloumn[item].type,
                filter: true,
              };
              if (getStrWidth(item) < 7) {
                obj.width = 170;
              } else if (getStrWidth(item) > 15) {
                obj.width = 300;
              } else {
                obj.width = getStrWidth(item) * 20;
              }
            }
            if (
              this.tableCloumn[item].category &&
              this.tableCloumn[item].category.length < 5
            ) {
              obj.type = "checkbox";
            }
            if (this.tableCloumn[item].type == "string") {
              obj.list = this.tableCloumn[item].category;
            }
            if (this.tableCloumn[item].type == "number") {
              obj.max = this.tableCloumn[item].max;
              obj.min = this.tableCloumn[item].min;
            }
            if (this.tableCloumn[item].type == "checkbox") {
              obj.list = this.tableCloumn[item].category;
            }
            arr_1.push(obj);
          }
        }
      });

      let obj = {};
      obj = {
        titleData: arr_1,
        contentData: [...this.tableData],
      };

      return obj;
    },
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      workflowId: (state) => state.workflowId.workflowId,
      resultNodeId: (state) => state.workflowId.resultNodeId,
      stateName: (state) => state.stateName.stateName,
      dataset: (state) => state.workflow.dataset.dataset,
    }),
  },

  created() {
    // 获取颜色
    this.colors = colors.colors;
  },

  mounted() {
    if (this.stateName != "FAGL") {
      this.getData();
      this.getFileData();
    }
  },

  methods: {
    download(fileName) {
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      let id = this.projectId;
      let isFile = true;
      this.getTableFile(workflowId, id, nodeId, name, isFile, fileName);
    },

    preview(fileName) {
      this.fileName = fileName;
      this.getData();
    },

    showInput() {
      this.inputName = !this.inputName;
    },

    createLabel(a) {
      let arr = [];
      a.forEach((item) => {
        if (item.type === "string" || item.type === "checkbox") {
          arr.push({
            field: item.name,
            type: "string",
            contain: item.condition,
          });
        } else if (item.type === "number") {
          arr.push({
            field: item.name,
            type: item.type,
            contain: item.condition,
          });
        }
      });
      // let attributeId = this.attributeId;
      // let id = Number(this.projectId);

      let labelData = {
        name: "",
        conditions: [],
      };
      labelData.conditions = arr;
      labelData.name = this.labelName;
      // let data = labelData;
      // console.log(data);
      this.labels.push(labelData);
      // this.$ProjectModels.postAddLabelModels(attributeId, id, data)
      //   .then(() => {})
    },

    // 请求表格数据
    getData() {
      let workflowId = this.workflowId;
      let nodeId = this.resultNodeId;
      let name = this.stateName;
      let id = this.projectId;
      let resultId = this.resultNodeId;
      let isFile = false;
      let blockSize = 1;
      let blockStart = 0;
      let blockNum = 100000;
      let filename = this.fileName;
      this.$progress.start();
      this.$ProjectModels
        .getTableResultModelsFile(
          workflowId,
          id,
          nodeId,
          name,
          isFile,
          blockSize,
          blockStart,
          blockNum,
          filename
        )
        .then((inx) => {
          [this.tableData, this.tableHeader, this.tableCloumn] = [
            inx.data.data,
            inx.data.header,
            inx.data.columnInfo,
          ];
          if (inx.data.length == 0) {
            this.$message.warning("No task");
          }
          this.$progress.finish();
        });
    },

    // 请求文件所有名
    getFileData() {
      let workflowId = this.workflowId;
      let nodeId = sessionStorage.getItem("getResultNodeId");
      let name = this.stateName;
      let id = this.projectId;
      this.$progress.start();
      this.$ProjectModels
        .getTaskNodeModels(workflowId, id, nodeId, name)
        .then((inx) => {
          this.fileNames = inx.data;
          if (inx.data.length == 0) {
            this.$message.warning("No task");
          }
          this.$progress.finish();
        });
    },

    // download表格文件
    getTableFile(workflowId, id, nodeId, name, isFile, fileName) {
      this.$progress.start();
      this.$ProjectModels
        .getTableResultModelsT(workflowId, id, nodeId, name, isFile, fileName)
        .then((ele) => {
          if (!ele) {
            return;
          }
          let url = window.URL.createObjectURL(ele.data); // 将对象转为一个url
          let link = document.createElement("a"); //创建一个a标签
          link.style.display = "none"; // 清除样式
          link.href = url; // 将
          link.setAttribute("download", fileName);
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);

          this.$progress.finish();
        });
    },

    // 提交新的节点数据
    submitData() {
      // 节点收集重新提交到vuex
      let objNode = {
        id: this.dataset.nodes[this.dataset.nodes.length - 1].id + 1,
        name: "dataManipulate",
        params: {
          filename: this.fileName,
          labels: this.labels,
        },
        color: "#808080",
        // id:0,
        input: [
          {
            id:
              "inNode" +
              (Number(this.dataset.nodes[this.dataset.nodes.length - 1].id) +
                1),
          },
        ],

        // name:"GQM",
        // num: 1,
        output: [
          {
            id:
              "outNode" +
              (Number(this.dataset.nodes[this.dataset.nodes.length - 1].id) +
                1),
          },
        ],
        x: this.dataset.nodes[this.dataset.nodes.length - 1].x + 200,
        y: this.dataset.nodes[this.dataset.nodes.length - 1].y + 200,
      };
      let objPath = {
        dotted: false,
        input:
          "inNode" +
          (Number(this.dataset.nodes[this.dataset.nodes.length - 1].id) + 1),
        output: this.dataset.nodes.find((ele) => {
          return this.resultNodeId == ele.id;
        }).output[0].id,
        ptype: "Q",
      };

      let nodes = [...this.dataset.nodes];
      let paths = [...this.dataset.paths];
      nodes.push(objNode);
      paths.push(objPath);
      this.$store.commit("getWorkflowInfo", {
        nodes: nodes,
        paths: paths,
        workflowId: this.dataset.workflowId,
        workflowName: this.dataset.workflowName,
      });
    },
  },

  watch: {
    resultNodeId() {
      if (this.stateName != "FAGL") {
        this.getData();
        this.getFileData();
      }
      this.labels = [];
    },
  },
};
</script>

